//  : PhotoCard

import React, { Component, createRef } from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { Carousel } from "antd";
import { LeftOutlined, RightOutlined } from "@ant-design/icons";

import { BoardBasicImage as BasicImg } from '../../../../../../styles/images';

/* Styled-Components */
/* PhotoBox   div  */
const PhotoWrapper = styled.div`
    width: 100%;
    height: 0;
    padding-top: 70%;
    position: relative;
    overflow: hidden;
`;

    /* PhotoBox      Link Wrapper */
    const PhotoLink = styled(Link)`
        width: 100%;
        padding-top: 70%;
        position: absolute;
        top: 0;
        overflow: hidden;

        /*   , Basic Image  */
        background-image: ${props => 
            props.hasPhoto ? 'none' : `url(${BasicImg})`};
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
    `;

        /*    padding     . */
        const PhotoContainer = styled.div`
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
        `;

            /*   Item */
            const PhotoItem = styled.div`
                height: ${props => props.imgHeight}px;
                width: 100%;

                background-image: ${props => `url(${props.imgURL})`};
                background-repeat: no-repeat;
                background-position: center center;
                background-size: cover;
            `;

    /*       */
    const BlackMask = styled.div`
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.25);
        opacity: 1;
        transition: opacity 0.25s;

        /* Basic   hover   */
        ${props => !props.hasHoverEffect && css`
            :hover {
                opacity: 0.5;
            }
        `}
    `;

    /*  ,    Container */
    const ButtonContainer = styled.div`
        position: absolute;
        top: ${props => props.posTop};
        width: 100%;
    `;

        /*    */
        const PhotoButton = styled.div`
            position: absolute;
            height: 2.5rem;
            width: 2.5rem;
            border-radius: 50%;

            background-color: white;

            font-weight: bold;

            display: flex;
            align-items: center;
            justify-content: center;

            :hover {
                top: -0.125rem;
                height: 2.75rem;
                width: 2.75rem;
                box-shadow: 0 0 10px rgba(0,0,0,.3);
            }
            cursor: pointer;
        `;

        /*    */
        const PrevButton = styled(PhotoButton)`
            left: 1rem;
            :hover {
                left: .875rem;
            }
        `;

        /*    */
        const NextButton = styled(PhotoButton)`
            right: 1rem;
            :hover {
                right: .875rem;
            }
        `;


/////////////////////////////


class PhotoBox extends Component {

    state = {}

    photoBoxRef = createRef();  //   ref
    carouselRef = createRef();  // ant design carousel (  ) ref

    componentDidMount() {
        window.addEventListener('resize', this._handleResize);                          //  (window)    .
        this.photoBoxRef.current.addEventListener('mouseover', this._handleMouseEnter);
        this.photoBoxRef.current.addEventListener('mouseleave', this._handleMouseLeave);
        this._handleResize();
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this._handleResize);
        this.photoBoxRef.current.removeEventListener('mouseenter', this._handleMouseEnter);
        this.photoBoxRef.current.removeEventListener('mouseleave', this._handleMouseLeave);
    }

    /* PhotoBox width   */
    _handleResize = (e) => {
        this.setState({
            ...this.state,
            containerWidth: this.photoBoxRef.current.offsetWidth,
        })
    }

    /* PhotoBox   isHover true */
    _handleMouseEnter = (e) => {
        this.setState({
            ...this.state,
            isHover: true,
        })
    }

    /* PhotoBox   isHover false */
    _handleMouseLeave = (e) => {
        this.setState({
            ...this.state,
            isHover: false,
        })
    }

    /* filelist , PhotoItem render  */
    _renderPhoto = (filelist, imgHeight) => {
        return filelist.map( file => {
            const { id, location } = file;
            return <PhotoItem imgURL={location} imgHeight={imgHeight} key={id} />
        } )
    }

    render() {

        const { filelist, boardName, postId } = this.props;
        const { containerWidth, isHover } = this.state;

        const hasPhoto = filelist.length !== 0;             //    
        const imgHeight = containerWidth * 0.7;             //    (  * 0.7)
        const buttonPosTop = (imgHeight / 2) - 18;          // absolute postion Button Container    
        const postURL = `/board/${boardName}/${postId}`;    //  url

        return (
            <PhotoWrapper ref={this.photoBoxRef} >
                <PhotoLink hasPhoto={hasPhoto} to={postURL}>
                    {
                        hasPhoto &&
                        
                        <PhotoContainer>
                            <Carousel ref={this.carouselRef} >
                            {
                                this._renderPhoto(filelist, imgHeight)
                            }
                            </Carousel>
                        </PhotoContainer>

                    }
                    <BlackMask hasHoverEffect={filelist.length > 1} />
                </PhotoLink>

                {
                    /*    */
                    filelist.length > 1 && isHover &&
                    <ButtonContainer posTop={`${buttonPosTop}px`} >
                        <PrevButton onClick={ () => this.carouselRef.current.prev() } > <LeftOutlined /> </PrevButton>
                        <NextButton onClick={ () => this.carouselRef.current.next() } > <RightOutlined /> </NextButton>
                    </ButtonContainer> 
                }
            </PhotoWrapper>
        )
    }
}

PhotoBox.propTypes = {
    filelist: PropTypes.array.isRequired,
    boardName: PropTypes.string.isRequired,
    postId: PropTypes.number.isRequired,
}

export default PhotoBox;