

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { InboxOutlined } from '@ant-design/icons';
import { Upload, Modal } from 'antd';

import { colors } from '../../../../../styles/variables'

import './DropZone.css'

/* Upload  DropZone Dragger  */
const { Dragger } = Upload;

/* FileReader    preview    */
const getBase64 = file => {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
    });
}

/* Styled Components */
/*   */
const Container = styled.div`
    margin-bottom: 3rem;
    padding: 2rem;

    border: 2px dashed ${colors.gray_line};
    border-radius: 12px;

    display: flex;
    flex-flow: column nowrap;
    align-items: center;
`

/*   */
const DropArea = styled(Dragger)`
    margin: 1rem auto;

    height: 15rem !important;
    width: 40rem !important;
`


/* React Component */
class DropZone extends Component {

    state = {
        previewVisible: false,
        previewImage: '',
    };

    /*    */
    handlePreview = async file => {
        /*    (url, preview  ) */
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }

        this.setState({
            previewImage: file.url || file.preview,     // url  url ,  File Reader  preview 
            previewVisible: true,
        });
    }
    
    handleCancel = () => this.setState({ previewVisible: false });

    render() {

        const {
            previewVisible,
            previewImage
        } = this.state;

        const { 
            files, 
            addFileData, 
            deleteFileData 
        } = this.props;

        /* Dropzone   props  */
        const props = {
            /*      */
            multiple: true,

            /*      */
            onRemove: file => {
                const isNew = !file.url
                deleteFileData(file, isNew)
            },

            /*     , app state   */
            beforeUpload: file => {
                /* Object  fileobj  , 
                   File  return        */
                const fileobj = {
                    ...file,
                    originFileObj: file,
                }
                addFileData(fileobj);
                return false;
            },

            /*   */
            onPreview: this.handlePreview,

            /*      */
            listType: 'picture-card',

            /*   
               (antd upload  fileList   .) */
            fileList: files
        };

        return (
            <Container className='photo-list-box' >
                <DropArea {...props}>
                <p className="ant-upload-drag-icon">
                    <InboxOutlined />
                </p>
                <p className="ant-upload-text">     </p>
                <p className="ant-upload-hint">
                       .
                </p>
                </DropArea>
                <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                    <img alt="example" style={{ width: '100%' }} src={previewImage} />
                    {/* alt     */}
                </Modal>
            </Container>
        );
    }
}

DropZone.propTypes = {
    
    files: PropTypes.array.isRequired,              //    

    addFileData: PropTypes.func.isRequired,
    deleteFileData: PropTypes.func.isRequired,
}

export default DropZone;