

//  : CarpoolContainer

import React, { Component } from "react";
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Modal } from "antd";

import BaseCalendar from "../Structure/Base_Calendar";
import { MenuBox, DateInfoBox, RoomModalBox } from "./SubComponents";
import { colors } from "../../styles/variables";
import { CARPOOL } from "../../constants/categories";

/* Styled Components */

/*    div  */
const BackGroundDiv = styled.div`
    width: 100%;
    min-width: 1280px;
    min-height: ${props => `${props.windowHeight}px`};
    padding-left: 15rem;
    background-color: ${colors.gray_bg};

    display: flex;
`;

/*   Box   div  */
const ContentArea = styled.div`
    width: 90%;
    min-width: 1030px;
    margin: 1.5rem auto;

    display: flex;
    flex-flow: column nowrap;

    @media (max-width: 1500px) {
        margin: 1rem auto;
    }
`;

/*  CalendarBox + InfoBox  */
const CalendarInfoArea = styled.div`
    margin-top: 1.5rem;
    flex: 1;
    
    display: flex;
    flex-flow: row nowrap;

    @media (max-width: 1500px) {
        margin-top: 0.75rem;
    }
`;

    /*  */
    const CalendarBox = styled.div`
        padding: 2rem;
        margin-right: 1.5rem;
        flex: 1;

        background-color: ${colors.white};

        display: flex;
        flex-flow: row nowrap;
        align-items: stretch;

        @media (max-width: 1500px) {
            margin-right: 0.75rem;
            padding: 1rem;
        }
    `;

        const CalendarContainer = styled.div`
            flex: 1;
        `;


/* React Component */
class CarpoolBoard extends Component {

    state = {
        modalVisible: false,
    }

    componentDidMount() {
        this.setState({
            windowHeight: window.innerHeight     //    
        })

        /*      . */
        window.addEventListener('resize', this._handleResize) 

        this.setState({
            ...this.state,
            isLoaded: true,
        })
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this._handleResize)
    }

    _handleResize = (e) => {
        this.setState({
            ...this.state,
            windowHeight: window.innerHeight
        })
    }

    /* Modal    */
    _openModalWindow = () => {
        this.setState({
            ...this.state,
            modalVisible: true
        })
    }

    _closeModalWindow = () => {
        this.setState({
            ...this.state,
            modalVisible: false
        })
    }

    render() {

        const { windowHeight, isLoaded, modalVisible } = this.state;

        const {
            curUser,
            totalOrMyOption,
            eventsObj,
            selectedDate,
            eventsOnSelectedDate,
            selectedEvent,
            eventDataToUpdate,

            initCalenderEvents,
            renderTotalEvents,
            renderMyEvents,
            changeClosedEvents,
            selectDate,
            storeClickedEventData,
            deleteEvent,
            storeEventUpdateData,
            updateEvent,
            closeOrCancleEvent,
            joinEvent,
            cancleJoinEvent,
        } = this.props

        return (
            isLoaded ?
            <BackGroundDiv windowHeight={windowHeight} >
                <ContentArea >
                    <MenuBox 
                        renderTotalEvents={renderTotalEvents}
                        renderMyEvents={renderMyEvents}
                        changeClosedEvents={changeClosedEvents}
                    />

                    <CalendarInfoArea>
                        <CalendarBox >
                            <CalendarContainer>
                                <BaseCalendar 
                                    category={CARPOOL}
                                    totalOrMyOption={totalOrMyOption}
                                    eventsObj={eventsObj}
                                    selectedDate={selectedDate}

                                    initCalenderEvents={initCalenderEvents}
                                    selectDate={selectDate}
                                    storeClickedEventData={storeClickedEventData}
                                    openModalWindow={this._openModalWindow}
                                />
                            </CalendarContainer>
                        </CalendarBox>
                        
                        <DateInfoBox 
                            selectedDate={selectedDate}
                            totalOrMyOption={totalOrMyOption}
                            eventsOnSelectedDate={eventsOnSelectedDate}

                            storeClickedEventData={storeClickedEventData}
                            openModalWindow={this._openModalWindow}
                        />
                    </CalendarInfoArea>

                    <Modal 
                        visible={modalVisible} 
                        onCancel={this._closeModalWindow} 
                        footer={null}
                        closable={false}
                    >
                        <RoomModalBox 
                            curUser={curUser}
                            selectedEvent={selectedEvent}
                            eventDataToUpdate={eventDataToUpdate}
                            
                            cancleModal={this._closeModalWindow} 
                            deleteEvent={deleteEvent} 
                            storeEventUpdateData={storeEventUpdateData}
                            updateEvent={updateEvent}
                            closeOrCancleEvent={closeOrCancleEvent}
                            joinEvent={joinEvent}
                            cancleJoinEvent={cancleJoinEvent}
                        />
                    </Modal>
                </ContentArea>
            </BackGroundDiv> :
            ''
        )
    }
}

CarpoolBoard.propTypes = {
    curUser: PropTypes.oneOfType([                         //   
        PropTypes.number, PropTypes.object
    ]).isRequired,
    totalOrMyOption: PropTypes.string.isRequired,       //   or  
    eventsObj: PropTypes.object.isRequired,             //    
    selectedDate: PropTypes.object.isRequired,          //    
    eventsOnSelectedDate: PropTypes.array.isRequired,   //     
    selectedEvent: PropTypes.object.isRequired,         //   
    eventDataToUpdate: PropTypes.object.isRequired,     //     

    initCalenderEvents: PropTypes.func.isRequired,      //      events  
    renderTotalEvents: PropTypes.func.isRequired,       //   
    renderMyEvents: PropTypes.func.isRequired,          //   
    changeClosedEvents: PropTypes.func.isRequired,      //    / 
    selectDate: PropTypes.func.isRequired,              //    
    storeClickedEventData: PropTypes.func.isRequired,   //     
    deleteEvent: PropTypes.func.isRequired,             //   
    storeEventUpdateData: PropTypes.func.isRequired,    //       
    updateEvent: PropTypes.func.isRequired,             //   
    closeOrCancleEvent: PropTypes.func.isRequired,      //      
    joinEvent: PropTypes.func.isRequired,               //    
    cancleJoinEvent: PropTypes.func.isRequired,         //     
}


export default CarpoolBoard;