

import React, { useState } from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import moment from 'moment';
import 'moment/locale/ko'

import { MoreButtonPopover, CommentInput } from "./";
import { UserPhoto } from "../../../Structure/CommonComponents";
import { colors } from "../../../../styles/variables";
import { useHover } from '../../../../useHooks'


/* Styled Components */
/* Comment  
   PhotoTextBox + BottomBox */
const CommentItemArea = styled.div`
    margin-bottom: 1rem;
    width: 100%;

    display: flex;
    flex-flow: column nowrap;
`;

    /*  ,    div */
    const PhotoTextBox = styled.div`
        width: 100%;

        display: flex;
        flex-flow: row nowrap;
        align-items: flex-start;
    `;

        /* UserPhoto      Box
           TextContainer + MoreButton */
        const TextBox = styled.div`
            margin-left: 0.5rem;
            flex: 1;

            display: flex;
            flex-flow: row nowrap;
            align-items: center;
        `;

            /*       div  */
            const TextContainer = styled.div`
                margin-right: 1rem;
                padding: 0.5rem 1rem;

                border-radius: 1rem;
                background-color: ${colors.white};

                line-height: 1.5rem;
                font-size: 0.875rem;
            `;

            /*       */
            const CreatedForSub = styled.div`
                margin-bottom: 0rem;
                margin-right: 1rem;

                align-self: flex-end;
                font-size: 0.75rem;
                white-space: nowrap;
            `;

    /*     text box */
    const BottomBox = css`
        margin: 0;
        margin-top: 0.5rem;
        margin-left: 3.75rem;
    
        font-size: 0.75rem;
        color: ${colors.gray_fontColor};

        cursor: default;

        display: flex;
        flex-flow: row nowrap;
        align-items: center;
    `;

        const UpdateBottomBox = styled.div`
            ${BottomBox}
        `;

            /* ''    */
            const CancleText = styled.span`
                color: ${colors.blue};
                
                cursor: pointer;
                :hover {
                    text-decoration: underline;
                }
            `;

        const RootBottomBox = styled.div`
            ${BottomBox}
        `;

            /*   /   */
            const ReplySeeButton = styled.span`
                cursor: pointer;
                :hover {
                    text-decoration: underline;
                }
            `;


/* Custom Functions */
const dateStrToDisplayTimeStr = (dateStr) => {
    return moment(dateStr).fromNow()
}


/* React Component */
const CommentItem = (props) => {

    const [ moreButtonVisible, setMoreButtonVisible ] = useState(false);
    const [ isUpdateMode, setUpdateMode ] = useState(false);

    const { 
        boardSocket,
        isRootComment,
        isMine, isReplySee,
    
        comment_id, user,
        description, created,
        subCommentList,

        changeReplySee,
    } = props;

    /* useHover: CommentItem Area   More Button visible true  */
    const handleHover = () => { if (isMine) { setMoreButtonVisible(true) } }
    const handleMouseLeave = () => { if (isMine) { setMoreButtonVisible(false) } }
    const commentItemRef = useHover(handleHover, handleMouseLeave);

    const { pic_location } = user;

    const displayTime = dateStrToDisplayTimeStr(created);
    const NumOfSubComment = subCommentList.length;

    return (
        <CommentItemArea ref={commentItemRef} isRootComment={isRootComment} >
            {
                isUpdateMode ?

                <CommentInput 
                    isUpdateMode={true}             

                    curUser={user}
                    comment_id={comment_id}
                    defaultValue={description}
                    boardSocket={boardSocket}

                    cancleUpdate={() => setUpdateMode(false)}
                /> :

                <PhotoTextBox>
                    <UserPhoto imgURL={pic_location} size={40} />
                    <TextBox>
                        <TextContainer> {description} </TextContainer>

                        {
                            !isRootComment &&
                            <CreatedForSub>
                                {displayTime}
                            </CreatedForSub>
                        }

                        <MoreButtonPopover 
                            boardSocket={boardSocket}
                            comment_id={comment_id}
                            user={user}
                            subCommentList={subCommentList}
                            moreButtonVisible={moreButtonVisible}

                            setUpdateMode={setUpdateMode}
                        /> 
                    </TextBox>
                </PhotoTextBox>
            }
            
            {
                /* Bottom Box:   /  ,   */
                isUpdateMode ?
                <UpdateBottomBox>
                    <CancleText onClick={() => setUpdateMode(false)} ></CancleText>
                    <span> Esc  .</span>
                </UpdateBottomBox> :

                isRootComment &&    // Root Comment  
                <RootBottomBox>
                    {displayTime}&nbsp;&nbsp;&middot;&nbsp;&nbsp;
                    <ReplySeeButton onClick={() => changeReplySee(isReplySee)} >
                    {
                        /*   : ' '
                               : 
                                  : 'n  '
                                  : '          */ 

                        isReplySee ? ' ' :
                        NumOfSubComment ? `${NumOfSubComment}  ` : ' '
                    }
                    </ReplySeeButton>
                </RootBottomBox>
            }
        </CommentItemArea>
    )
}


CommentItem.propTypes = {
    boardSocket: PropTypes.object.isRequired,   // Board Socket
    isRootComment: PropTypes.bool.isRequired,   // Root  
    isMine: PropTypes.bool.isRequired,          //   (  )
    isReplySee: PropTypes.bool,                 //   

    comment_id: PropTypes.number.isRequired,    //  ID
    user: PropTypes.object.isRequired,          //  
    description: PropTypes.string.isRequired,   //  
    created: PropTypes.string.isRequired,       //  
    subCommentList: PropTypes.array,            //   array

    changeReplySee: PropTypes.func,             //     
}

CommentItem.defaultProps = {
    isReplySee: false,
    subCommentList: [],
    changeReplySee: () => {},
}

export default CommentItem;