//  : BoardDetailContainer

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { 
    HeaderBox,
    PhotoBox,
    DescriptionBox,
    CommentBox,
} from "./subcomponents";

import { colors } from "../../styles/variables";


/* Styled Components */

/*     div  */
const BackgroundDiv = styled.div`
    width: 100%;
    padding-left: 15rem;
    background-color: ${colors.gray_bg};
`;

/*     div 
         , content         .
       70%      */
const ContainerDiv = styled.div`
    width: 70%;
    min-height: ${props => props.minHeight};
    min-width: 50rem;
    max-width: 60rem;
    margin: 0 auto;
    padding: 3rem 2rem;

    background-color: ${colors.white};

    /*   */
    box-shadow: 0 0 12px rgba(80,80,80,.1);
    /* border-left: 2px solid ${colors.gray_bg};
    border-right: 2px solid ${colors.gray_bg}; */

    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
`;

    const PostingBox = styled.div`
        display: flex;
        flex-flow: column nowrap;
    `;


/* React Component */

class BoardDetail extends Component {

    state = {}

    componentDidMount() {
        /*      . */
        window.addEventListener('resize', this._handleResize) 
        
        this._handleResize();
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this._handleResize)
    }

    _handleResize = (e) => {
        this.setState({
            ...this.state,
            clientHeight: document.documentElement.clientHeight
        })
    }

    render() {
        
        const { clientHeight } = this.state;

        const {
            boardSocket,        // Board Socket
            curUser,            //   
            post,               //  
            board,              //   
            commentList,        // comment data

            deletePost,         //   
        } = this.props;

        const {
            id,                 //  id

            /* PhotoBox   */
            filelist,           //     (array)

            /* TextBox   */
            title,              //  (string)
            user,               //    (object)
            created,            //  (string)
            condition,          //  
            description,        //  

            price,              // Market Post    
        } = post;

        const isPhoto = filelist.length > 0;        // filelist    true
        // const isOwner = curUser.id === user.id;     //    
        const isOwner = curUser.email === user.email;     //     (  id  )

        const headerMethods = { deletePost };

        return (
            <BackgroundDiv>
                <ContainerDiv minHeight={`${clientHeight}px`} >
                    <PostingBox>
                        <HeaderBox
                            postId={id}
                            isPhoto={isPhoto}
                            isOwner={isOwner}
                            board={board}
                            title={title}
                            user={user}
                            created={created}
                            condition={condition}
                            price={price}
                            headerMethods={headerMethods}
                        />
                        { isPhoto ? <PhotoBox filelist={filelist} /> : '' }
                        <DescriptionBox description={description} isPhoto={isPhoto} />
                    </PostingBox>
                    <CommentBox 
                        boardSocket={boardSocket}
                        curUser={curUser}
                        board={board}
                        post_id={id}
                        commentList={commentList}
                    />
                </ContainerDiv>
            </BackgroundDiv>
        )
    }
}

BoardDetail.propTypes = {
    boardSocket: PropTypes.object.isRequired,   // Board Socket
    curUser: PropTypes.object.isRequired,       //   
    board: PropTypes.string.isRequired,         //  
    post: PropTypes.object.isRequired,          //  
    commentList: PropTypes.array,               //  

    deletePost: PropTypes.func.isRequired,      //   
}

BoardDetail.defaultProps = {
    commentList: [],
}

export default BoardDetail;
