//  : components/Board

import React, { Component } from "react";
import PropTypes from 'prop-types';
import styled from 'styled-components';

import { FakeCard, PhotoBox, ContentBox } from "./Cards";
import { colors } from "../../../../../styles/variables";
import { MARKET } from "../../../../../constants/categories";


/* Styled Components */
const PostCardArea = styled.div`
    margin: 1.5rem .75rem;
    flex-grow: 1;
    flex-basis: 20rem; 
    
    background-color: ${colors.white};
    box-shadow: 0 0 10px rgba(0,0,0,.1);
    
    display: flex;
    flex-flow: column nowrap;  
`;

    /*  div  */
    const Card = styled.div`
        display: flex;
        flex-flow: column nowrap;
    `;


/* React Component */
class PostCard extends Component {

    render() {

        const { 
            boardName, 
            isFake,
            id,
            title,
            user,
            created,
            description,
            condition,
            filelist,
            price,
        } = this.props

        const isMarketCard = boardName === MARKET;
        /* Market   Photo Card, Network     PhotoCard */
        const isPhotoCard = isMarketCard || filelist.length;

        const contentBoxProps = {
            isPhotoCard,
            postId: id, boardName,
            title, user, created, condition,
            description,
            price,
        };

        return (
            <PostCardArea >
            {
                isFake ?
                <FakeCard/> :
                <Card>
                    { isPhotoCard ? <PhotoBox filelist={filelist} boardName={boardName} postId={id} /> : '' }
                    <ContentBox {...contentBoxProps} />
                </Card>
            }
            </PostCardArea>
        )
    }
}

PostCard.propTypes = {
    boardName: PropTypes.string.isRequired, //  
    id: PropTypes.number.isRequired,        //   DB  id
    title: PropTypes.string.isRequired,     // 
    user: PropTypes.object.isRequired,      //   
    created: PropTypes.string.isRequired,   //  
    condition: PropTypes.string,            //    ( ,   ...)
    description: PropTypes.string,          //  
    filelist: PropTypes.array,              //   
    isFake: PropTypes.bool.isRequired,      // fake 

    price: PropTypes.string,                // [market]  
}

PostCard.defaultProps = {
    description: '',
    filelist: [],
    price: '',
    condition: ''
}


export default PostCard