

import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Input } from 'antd';

import { TITLE } from "../../../../../constants/edit_RoomInfo_DataKeys";

/* Styled Components */

const TitleTextArea = styled(Input.TextArea)`
    margin-bottom: 1rem;

    font-size: 2rem;

    /* TextArea  */
    resize: none;
    border: none;
    outline: 0;

    /* Ant-Design    */
    :focus {
        border: none;
        box-shadow: none;
    }

    /*    */
    @media (max-width: 1500px) {
        margin-bottom: 0;
        font-size: 1.5rem;
    }
`;


/* React Component */

const RoomTitleInput = ({storeCarpoolData}) => {

    return (
        <TitleTextArea 
            placeholder="  ."
            autoSize={true}
            onChange={(e) => storeCarpoolData(TITLE, e.target.value)}
        />
    )
}

RoomTitleInput.propTypes = {
    storeCarpoolData: PropTypes.func.isRequired,        // Carpool  Room Info Data  
}

export default RoomTitleInput;