import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { colors } from "../../../styles/variables";


/* Styled Components */

/*  Box   div 
   padding      
   position: relative; ,  position   */
const BoxArea = styled.div`
    margin-top: 2rem;
    width: 100%;
    padding-top: 60%;
    border-bottom: 1px solid ${colors.light_gray_line};

    position: relative;
`;

/* position: absolute;  width, height, top  ,
           div  .
   PhotoAndButtonDiv + SmallPhotoList  flex-box */
const BoxContainer = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;

    display: flex;
    flex-flow: column nowrap;
`;

/* PhotoContainer +     flex-box 
   flex: 1;  ,         */
const PhotoAndButtonDiv = styled.div`
    width: 100%;
    flex: 1;

    position: relative;
`;

/*   .
   size contain ,         */
const PhotoContainer = styled.div`
    height: 100%;

    /* transition: background-image 0.3s; */

    background-image: ${props => {
        return `url(${props.imgURL})`;
    }};
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
`;

/* prev, next   flex box
   absolute position ,    */
const ButtonDiv = styled.div`
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;

    display: flex;
    flex-flow: row nowrap;
`;

/* prev, next  */
const Button = styled.div`
    flex: 1;
    cursor: pointer;
`;

/*     div 
   9    .
   [ 9   ,     ] */
const SmallPhotoList = styled.div`
    width: 32rem;
    height: 5rem;
    padding-top: 1rem;
    margin: 0 auto;

    /* box-shadow: 0 0 3px rgba(0,0,0,.4); */

    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
`;

/*   */
const SmallPhoto = styled.div`
    width: 2.5rem;
    height: 2.5rem;
    margin: 0 0.5rem;
    border-radius: 0.5rem;

    cursor: pointer;
    transition: all 0.3s;
    
    background-image: ${props => {
        return `url(${props.imgURL})`
    }};
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

    
    border: ${props => {
        return props.isActivate ? 'solid 1px black' : 'none'
    }};
    opacity: ${props => {
        return props.isActivate ? '1' : '0.5'
    }};
    box-shadow: ${props => {
        return props.isActivate ? '0 0 3px rgba(0,0,0,.4);' : ''
    }};
`;

/* React Component */

class PhotoBox extends Component {

    state = {
        photoOrder: 0   //     
    }

    /* prev   
       photoOrder  1 . (  ) */
    _handlePrevBtn = () => {
        let numOfPhotos = this.props.filelist.length;

        this.setState({
            ...this.state,
            photoOrder: (this.state.photoOrder + numOfPhotos - 1) % numOfPhotos 
        })
    }

    /* next   
       photoOrder  1 . (  ) */
    _handleNextBtn = () => {
        let numOfPhotos = this.props.filelist.length;

        this.setState({
            ...this.state,
            photoOrder: (this.state.photoOrder + 1) % numOfPhotos 
        })
    }

    /* photoList photo   .
              . */
    _handlePhotoBtn = (e, idx) => {
        this.setState({
            ...this.state,
            photoOrder: idx 
        })
    }

    _renderSmallPhoto = (filelist) => {
        return filelist.map((photo, idx) => {
            let isActivate = idx === this.state.photoOrder;
            return (
                <SmallPhoto
                    imgURL={photo.location}
                    onClick={(e) => this._handlePhotoBtn(e, idx)}
                    isActivate={isActivate}
                    key={idx}
                />
            )
        })
    }

    render() {
        const { filelist } = this.props;
        const { photoOrder } = this.state;

        return (
            /*   ,  */
            filelist[0] ?

            <BoxArea>
                <BoxContainer>
                    <PhotoAndButtonDiv>
                        <PhotoContainer 
                            imgURL={filelist[photoOrder].location} 
                        />
                        <ButtonDiv>
                            <Button onClick={this._handlePrevBtn} />
                            <Button onClick={this._handleNextBtn} />
                        </ButtonDiv>
                    </PhotoAndButtonDiv>
                    <SmallPhotoList>
                        {this._renderSmallPhoto(filelist)}
                    </SmallPhotoList>
                </BoxContainer>
            </BoxArea> :

            <div />
        )
    }
}

PhotoBox.propTypes = {
    filelist: PropTypes.array.isRequired    //    array
}

export default PhotoBox