//  : compoents/Structure/Header

import React, { Component } from "react";
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { SearchOutlined } from '@ant-design/icons';

import { colors } from "../../../../styles/variables";

/* Styled Components */

/*  (input + button) */
const SearchBarBox = styled.div`
    width: 70%;
    max-width: 36rem;
    height: 2rem;
    
    background-color: ${colors.white};
    border-radius: 1rem;
    overflow: hidden;
    
    display: flex;
    flex-flow: row nowrap;
`;

/* Text   */
const SearchInput = styled.input`
    flex: 1;
    padding: .6em 1em;
    
    border: none;
    font-size: .875rem;

    :focus {
        outline: none;
        color: ${colors.blue};
        text-shadow: 0px 0px 0px ${colors.black};  
        -webkit-text-fill-color: transparent;
    }
`

/* Search Button */
const SearchButton = styled.button`
    flex: 0 4rem;
    
    color: ${colors.blue};
    background-color: ${colors.gray_bg};

    border: 0;
    outline: 0;
    cursor: pointer;
    /* border-left: 2px solid #f4f4f4;  */
`;


/* React Component */

class SearchBar extends Component {

    state = {
        keyword: ''
    }
    
    /*    , get board   */
    _handleClick = () => {
        const {
            board,
            getBoardRequest
        } = this.props;

        getBoardRequest(board, 0, this.state.keyword);
    }

    /*   , state    */
    _handleInput = (e) => {
        this.setState({
            keyword: e.target.value
        })
    }

    /*  enter  _handleClick   */
    _handleEnter = (e) => {
        if (e.keyCode === 13) {
            this._handleClick();
        }
    }

    render() {
        return (
            <SearchBarBox>
                <SearchInput 
                    type='text' 
                    placeholder=' .'
                    onChange={this._handleInput} 
                    onKeyDown={this._handleEnter}
                />
                <SearchButton onClick={this._handleClick} ><SearchOutlined /></SearchButton>
            </SearchBarBox>
        );
    }
}

SearchBar.propTypes = {
    board: PropTypes.string.isRequired,             //  board

    getBoardRequest: PropTypes.func.isRequired,     // board get   
}

export default SearchBar;