//  : MarketContainer, NetworkContainer

import React from "react";
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { SearchOutlined } from '@ant-design/icons';

import { colors } from '../../../../styles/variables';


/* Styled Components */

/*  
   isHeaderOn   visible: hidden,
     visible .  */
const CircleArea = styled.div`
    width: 3.5rem;
    height: 3.5rem;
    position: fixed;
    bottom: 2rem;
    right: 2rem;
    border-radius: 50%;
    background-color: ${colors.blue};
    cursor: pointer;

    /*     */
    display: flex;
    align-items: center;
    justify-content: center;

    /* props  visible  opacity  */
    opacity: 0.85;
    visibility: visible;

    ${props =>
      props.isHeaderOn && 
      css`
        opacity: 0;
        visibility: 'hidden';
      `};

    transition: opacity 0.3s;
`;

const StyledIcon = styled(SearchOutlined)`
    color: ${colors.white};
    font-size: 1.25rem;
    font-weight: bold;
    opacity: 1;
`;


/* React Compoent */
const SearchIcon = ({isHeaderOn, headerOn}) => {
    return (
        <CircleArea
            isHeaderOn={isHeaderOn}
            onClick={headerOn}
        >
            <StyledIcon />
        </CircleArea>
    )
}

SearchIcon.propTypes = {
    isHeaderOn: PropTypes.bool.isRequired,      //    
    
    headerOn: PropTypes.func.isRequired,        //    
}

export default SearchIcon;