//  : components/BoardDetail.js

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Divider } from "antd";

import { CommentBranch, CommentInput } from './Comment_Materials'
import { colors } from "../../../styles/variables";

/* Styled Components */

/* CommentBox  div */
const CommentArea = styled.div`
    position: relative;
    margin-bottom: 1rem;
    padding: 1.5rem 1rem;
    width: 100%;
    min-height: 10rem;

    border-radius: 8px;
    background-color: ${colors.gray_bg};

    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
`;

    /*    +      */
    const CommentHeader = styled.div`
        width: 100%;
        margin-bottom: 1.5rem;
        height: 2rem;

        line-height: 2rem;
        font-size: 1.5rem;
    `;

    /* CommentInput   */
    const InputDivider = styled(Divider)`
        height: 2px;
    `;

/////////////////////////////////////////

/* React Components */

class CommentBox extends Component {

    //   (isMoreSee) 

    componentDidMount() {
    }

    // map  Comment Array  render 
    _renderComments = (commentList) => {

        const { 
            curUser,
            board,
            post_id,
            boardSocket,
        } = this.props;

        return commentList.map( (comment) => {
            const {
                id, user, created,
                description,
                subCommentList
            } = comment;

            return (
                <CommentBranch
                    curUser={curUser}
                    board={board} post_id={post_id}
                    boardSocket={boardSocket}

                    comment_id={id} key={id}
                    user={user} created={created}
                    description={description}
                    subCommentList={subCommentList}
                />
            )
        })
    }

    render() {

        const { 
            boardSocket,
            curUser,

            board,
            post_id,
            commentList,
        } = this.props;
        const numOfComments = commentList.length;

        return (
            <CommentArea>
                
                {/*   +    */}
                <CommentHeader>
                    {numOfComments}  
                </CommentHeader>

                {/*    */}
                <CommentInput 
                    isSubComment={false} 
                    
                    boardSocket={boardSocket}
                    curUser={curUser}

                    board={board}
                    post_id={post_id}
                />

                {/*        */}
                {/* <BottomOfInputDiv /> */}
                <InputDivider />
                
                {/*  render */}
                {
                    numOfComments ?
                    this._renderComments(commentList) :
                    '  .'
                }

                {/* //////////////////////////// */}
            </CommentArea>
        )
    }
}


CommentBox.propTypes = {
    boardSocket: PropTypes.object.isRequired,       // Board Socket
    curUser: PropTypes.object.isRequired,           //   

    board: PropTypes.string.isRequired,             //  
    post_id: PropTypes.number.isRequired,           //  id          
    commentList: PropTypes.array.isRequired,        //     array
}

// CommentBox.defaultProps = {
//     commentList: []
// }

export default CommentBox;