

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Button } from "antd";

import { colors } from "../../../../styles/variables";

/* Styled Components */

const Container = styled.div`
    padding: 0 2rem;

    display: flex;
    flex-flow: row nowrap;
    align-items: center;
`;

    const DateBox = styled.div`
        font-weight: bold;
        margin-right: 1rem;
    `;

    /*   /   */
    const EndButton = styled(Button)`
        height: 2rem;
        padding: 0.5625rem 1rem;
        margin: 0 1rem;

        background-color: ${colors.blue};

        line-height: 0.875rem;
        font-size: 0.875rem;
    `;


class RightBox extends Component {

    /*   /    */
    _handleEndClick = () => {
        
        const { 
            id, editCategory, isCarpool,

            title, description,
            price,
            files, deletedFileIDs,
            
            selectedDate, roomInfoData,

            EditPostRequest,
            postCarpoolEvent,
        } = this.props;

        if ( isCarpool ) {
            const { 
                title, departure, destination, 
                start_time, meeting_place, 
                contact, description 
            } = roomInfoData

            postCarpoolEvent(title, departure, destination, selectedDate, start_time, meeting_place, contact, description);
        }

        else {
            /*   file       
               (url    )  . */
            const uploadFiles = files.filter( file => !file.url )

            id ?    // id   ,   
            EditPostRequest(editCategory, title, price, description, uploadFiles, id, deletedFileIDs) :
            EditPostRequest(editCategory, title, price, description, uploadFiles)
        }
    }


    /* Date => 'yyyy mm dd O' */
    _DateToString = (date) => {

        const dd = date.getDate();
        const mm = date.getMonth() + 1;
        const yyyy = date.getFullYear();
        let day = date.getDay();

        switch (day) {
            case 0: day = ''; break;
            case 1: day = ''; break;
            case 2: day = ''; break;
            case 3: day = ''; break;
            case 4: day = ''; break;
            case 5: day = ''; break;
            case 6: day = ''; break;
            default: break;
        }

        return `${yyyy} ${mm} ${dd} (${day})`;
    }

    render () {

        const {
            isCarpool,
            isNew,

            selectedDate,
        } = this.props;

        const dateString = this._DateToString(selectedDate);

        return (
            <Container>
                { isCarpool ?
                    <DateBox> {dateString}  </DateBox> :
                    ''
                }
    
                {/*   /   */}
                <EndButton type='primary' shape='round' onClick={this._handleEndClick} >
                    { isCarpool ?
                        '' :
                        isNew ? ' ' : ' ' }
                </EndButton>
            </Container>
        )
    }   
}

RightBox.propTypes = {
    isCarpool: PropTypes.bool.isRequired,           //   
    isNew: PropTypes.bool,                          // Create / Update 
    id: PropTypes.number,                           // Update    id
    editCategory: PropTypes.string.isRequired,      //   

    /*     */
    title: PropTypes.string.isRequired,             // Title Data
    price: PropTypes.string.isRequired,
    files: PropTypes.array.isRequired,              // Files Data
    deletedFileIDs: PropTypes.array.isRequired,     //  ,   id 
    description: PropTypes.string.isRequired,       // Description Data

    /* Carpool   */
    selectedDate: PropTypes.object,                 // Carpool    
    roomInfoData: PropTypes.object,                 // Carpool  

    /* Methods */
    EditPostRequest: PropTypes.func.isRequired,     // Post Create / Update function
    postCarpoolEvent: PropTypes.func.isRequired,    // Carpool Event  Post Action
}


export default RightBox;