

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { InfoCircleOutlined } from '@ant-design/icons';
import { Divider, Modal } from "antd";

import { colors } from "../../../styles/variables";
import EventListItem from "./DateInfo_Sub/EventListItem";
import { ACTIVE, GUEST, OWNER, CLOSED } from '../../../constants/calendar_consts';


/* Styled Components */

/*   div  (   ) */
const WholeBoxArea = styled.div`
    padding: 1.25rem;
    width: 18rem;

    background-color: ${colors.white};
    
    overflow: hidden;

    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: stretch;

    @media (max-width: 1500px) {
        width: 15rem;
    }
`;

    /* InfoBox      div  */
    const UpperBox = styled.div`
        flex: 1;
        display: flex;
        flex-flow: column nowrap;
        align-items: center;
    `;

        /*      */
        const DateTitleBox = styled.div`
            margin-bottom: 0.5rem;
            display: flex;
            justify-content: center;

            font-size: 1.25rem;
            font-weight: bold;
        `;

        /* DateTitle    */
        const StyledDivier = styled(Divider)`
            margin-top: 0;
            width: 10rem;
            min-width: 8rem;
        `;

        /*      */
        const EventsListBox = styled.div`
            flex: 1;
            flex-basis: 0;
            margin-bottom: 2rem;
            width: 100%;
            overflow-y: auto;

            display: flex;
            flex-flow: column nowrap;
            align-items: flex-start;
        `;

            /*        */
            const NoEventBox = styled.div`
                /* margin-top: 1rem; */
                width: 100%;

                display: flex;
                flex-flow: column nowrap;
                align-items: center;
            `;

    /*    div  */
    const InfomationBox = styled.div`
        position: relative;
        width: 12rem;
        padding: 0.75rem;
        padding-top: 1.25rem;
        margin: 0 auto;
        
        border: 1px solid ${colors.gray_line};

        display: flex;
        flex-flow: column nowrap;
        align-items: center;
    `;

        /* Infomation Box Title */
        const InfoTitle = styled.div`
            position: absolute;
            top: -1rem;
            padding: 0 0.25rem;

            font-size: 1.25rem;
            font-weight: bold;

            background-color: ${colors.white};
        `;

        const InfoList = styled.div`
            display: flex;
            flex-flow: column nowrap;
        `;

        const UsingInfo = styled.div`
            cursor: pointer;

            display: flex;
            flex-flow: row nowrap;
            align-items: center;
        `;

            const InfoIcon = styled(InfoCircleOutlined)`
                margin-right: 0.5rem;
            `;


/* React Component */
class DateInfoBox extends Component {

    state = {
        modalVisible: false
    }

    /* Date => yyyy-mm-dd */
    _dateToStr = (date) => {
        const mm = date.getMonth() + 1 ;
        const dd = date.getDate();

        return `${mm} ${dd} `;
    }

    /*   render  */
    _renderEventList = ( eventDataList ) => {

        const { totalOrMyOption, storeClickedEventData, openModalWindow } = this.props

        return eventDataList.map( event => {
            const { id, label } = event

            return (
                <EventListItem
                    key={id}
                    label={label} event={event}
                    totalOrMyOption={totalOrMyOption}
                    storeClickedEventData={storeClickedEventData}
                    openModalWindow={openModalWindow}
                />
            )
        } )
    }

    /* Carpool Info    */
    _OpenInfoModal = () => {
        this.setState({
            ...this.state,
            modalVisible: true
        })
    }

    _cancleInfoModal = (e) => {
        console.log(e)
        this.setState({
            ...this.state,
            modalVisible: false
        })
    }

    render() {

        const { modalVisible } = this.state;
        const { selectedDate, eventsOnSelectedDate } = this.props;

        const dateStr = this._dateToStr(selectedDate)

        return (
            <WholeBoxArea>
                <UpperBox>
                    <DateTitleBox>{dateStr}</DateTitleBox>
                    <StyledDivier />
                    <EventsListBox>
                        {
                            eventsOnSelectedDate.length === 0 ?
                            <NoEventBox>  .</NoEventBox> :
                            this._renderEventList(eventsOnSelectedDate)
                        }
                    </EventsListBox>
                </UpperBox>

                <InfomationBox>
                    <InfoTitle>Infomation</InfoTitle>
                    <InfoList>
                        <EventListItem label={ACTIVE} infoText=' ' />
                        <EventListItem label={GUEST} infoText='  ' />
                        <EventListItem label={OWNER} infoText='  ' />
                        <EventListItem label={CLOSED} infoText=' ' />
                        <UsingInfo onClick={this._OpenInfoModal}>
                            <InfoIcon />
                              
                        </UsingInfo>
                        <Modal
                                visible={modalVisible}
                                onCancel={this._cancleInfoModal}
                                footer={null}
                                maskClosable={true}
                            >
                            hi
                        </Modal>
                    </InfoList>
                </InfomationBox>
            </WholeBoxArea>
        );
    }
}

DateInfoBox.propTypes = {
    totalOrMyOption: PropTypes.string.isRequired,       //   or  

    selectedDate: PropTypes.object.isRequired,          //  
    eventsOnSelectedDate: PropTypes.array.isRequired,   //     

    storeClickedEventData: PropTypes.func.isRequired,   //     
    openModalWindow: PropTypes.func,                    // Modal   
}

export default DateInfoBox;