//  : components/BoardDetail/subcomponents/CommentBox.js

import React, { Component, createRef } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { message } from 'antd';
import { SendOutlined } from "@ant-design/icons";

import { UserPhoto, UitdaTextArea } from "../../../Structure/CommonComponents";
import { colors } from "../../../../styles/variables";


/* Styled Components */

/* CommentInput     div 
   (CommentItemPhoto + CommentInputTextArea)
   isDisplay(bool)   false  display none . */
const CommentInputArea = styled.div`
    margin: 0;

    display: flex;
    flex-flow: row nowrap;
`;

/* TextArea Send   div . (    div)
         flex item 
   TextArea + SendButton  flex box. */
const CommentInputTextArea = styled.div`
    margin-left: 0.5rem;

    border-radius: 1rem;
    background-color: ${colors.white};
    padding: 0.25rem 0.75rem ;

    flex: 1;
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
`;

    /* CommentInput TextArea    flex item */
    const TextAreaDiv = styled.div`
        flex: 1;
    `;

    /*      */
    const SendButton = styled(SendOutlined)`
        margin-left: 0.5rem;
        height: 1.5rem;
        width: 1rem;

        cursor: pointer;
    `;


/* React Component */

class CommentInput extends Component {

    // state text  
    state = { content: '' }

    textAreaRef = createRef()

    _storeTextToState = (type, value) => {
        this.setState({
            content: value
        })
    }

    /*   POST   DB  ,
       Post Detail   GET   . or  */
    _handleSend = () => {
        const {
            boardSocket,
            isUpdateMode,

            curUser,
            board,
            post_id,
            
            comment_id,
            parent_comment,     // subComment  

            cancleUpdate,
        } = this.props;

        const { content } = this.state;

        const { email } = curUser;

        /*      */
        if(!content) { message.warning('  '); return; }

        /* Update Comment Action */
        if (isUpdateMode) {
            const data = { email, comment_id, description: content }
            boardSocket.emit('comment update', data);
            cancleUpdate();
        }

        /* Create Comment Action */
        else {
            let data = {
                email, description: content, 
                type_board: board, board_id: post_id, 
            };
            data = parent_comment ? 
            { ...data, is_re_comment: true, parent_comment: parent_comment} :
            { ...data, is_re_comment: false }

            boardSocket.emit('comment create', data);
            this.textAreaRef.current.clearTextArea();
        }
    }

    /*     (Enter, Esc)
       KeyDown  + Enter ,      */
    _handleKeyPress = (e) => {
        switch(e.key) {
            case 'Enter':
                if(e.shiftKey) { break; }
                e.preventDefault(); this._handleSend(); break;

            default: return;        
        }
    }

    /* KeyPress Esc    */
    _handleKeyDown = (e) => {

        const { 
            isUpdateMode,
            cancleUpdate
        } = this.props;

        switch(e.key) {
            case 'Escape':
                if ( !isUpdateMode ) { return }
                cancleUpdate(); break;

            default: return;
        }
    }

    render() {

        const { 
            curUser,
            isUpdateMode,

            defaultValue
        } = this.props;

        const { pic_location } = curUser;

        return (
            <CommentInputArea >
                <UserPhoto imgURL={pic_location} size={40} />
                <CommentInputTextArea>
                    <TextAreaDiv
                        onKeyPress={this._handleKeyPress}
                        onKeyDown={this._handleKeyDown}
                    >
                        <UitdaTextArea 
                            ref={this.textAreaRef}
                            size='100%' 
                            isUnderLine={false} 
                            placeHolder=' .'
                            defaultText={defaultValue}
                            letFocus={isUpdateMode}

                            storeDataFunc={this._storeTextToState}
                        />
                    </TextAreaDiv>
                    <SendButton onClick={this._handleSend} />                   
                </CommentInputTextArea>
            </CommentInputArea>
        )
    }
}


CommentInput.propTypes = {
    isUpdateMode: PropTypes.bool,                   //   Input 
    boardSocket: PropTypes.object.isRequired,       // Board Socket
    curUser: PropTypes.object,                      //   
    board: PropTypes.string,                        //  
    post_id: PropTypes.number,                      //  id
    
    parent_comment: PropTypes.number,               //  ,   id
    comment_id: PropTypes.number,                   //   ,   id
    defaultValue: PropTypes.string,                 //   ,    

    cancleUpdate: PropTypes.func,                   //    
}

CommentInput.defaultProps = {
    isUpdateMode: false,
    curUser: 0,
    board: '',
    post_id: 0,
    parent_comment: 0,
    comment_id: 0,
    defaultValue: '',

    cancleUpdate: () => {},
}


export default CommentInput;