

import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Input } from "antd";

import { DESCRIPTION } from "../../../../../constants/edit_RoomInfo_DataKeys";
import { colors } from "../../../../../styles/variables";

/* Styled Components */
const WholeBox = styled.div`
    padding: 0.5rem;
    flex: 1;
    border-radius: 1rem;

    background-color: ${colors.gray_bg};
`;

    const DescriptionTextArea = styled(Input.TextArea)`

        font-size: 1rem;
        background-color: inherit;

        /* TextArea  */
        resize: none;
        border: none;
        outline: 0;

        /* Ant-Design    */
        :focus {
            border: none;
            box-shadow: none;
        }

        /*    */
        /* @media (max-width: 1500px) {
            
        } */
    `;


/* React Component */
const RoomDescriptionBox = ({storeCarpoolData}) => {

    return (
        <WholeBox>
            <DescriptionTextArea 
                placeholder="  ."
                autoSize={true}
                onChange={(e) => storeCarpoolData(DESCRIPTION, e.target.value)}
            />
        </WholeBox>
    )
}

RoomDescriptionBox.propTypes = {
    storeCarpoolData: PropTypes.func.isRequired,    // Carpool  Room Info Data  
}

export default RoomDescriptionBox