

//  : SideBar

import React from "react";
import PropTypes from 'prop-types';
import styled from 'styled-components';

import MenuItem from "./MenuItem";


/* Styled Components */
const MenuBox = styled.div`
    padding: 0;
    margin: 0;

    display: flex;
    flex-flow: column nowrap;
`;


/* React Component */
const Menu = ({selectedTopic, topicData}) => {
    
    return (
        <MenuBox >
            {topicData.map((data, i) => {
                const { titleText, url, category } = data;
                
                return(
                    <MenuItem
                        titleText={titleText}
                        url={url}
                        category={category}
                        isSelected={category === selectedTopic}
                        key={i}
                    />
                )
            })}
        </MenuBox>
    )
}

Menu.propTypes = {
    selectedTopic: PropTypes.string,        //   topic  
    topicData: PropTypes.array.isRequired   //    
}

Menu.defaultProps = {
    selectedTopic: "HOME"
}

export default Menu;