import React from 'react';
import styled from 'styled-components';
import { Input, Radio } from 'antd';
import PropTypes from 'prop-types';

/* Styled Components */
const PriceTextArea = styled(Input.TextArea)`

    font-size: 1.5em;

    /* TextArea  */
    resize: none;
    border: none;
    outline: 0;

    width: 180px;

    /* Ant-Design    */
    :focus {
        border: none;
        box-shadow: none;
    }
`

/* React Component */
const options = [
  { label: ' ', value: ' ' },
  { label: ' ', value: ' ' },
  { label: ' ', value: ' ' },
];

class EditPrice extends React.Component {
  
    state = { disabled: false, value: ' ' };

    onChange = e => { 
        this.setState({ value: e.target.value })
        if (e.target.value == ' ' || e.target.value == ' '){
            this.setState({ disabled: true  })
        }
        else {
            this.setState({ disabled: false })
        }
    };

    componentDidUpdate(prevProps, prevState) {
        if(this.state.value != ' '){
            this.props.storePriceData(this.state.value);
        }
    }

    render() {

        const { price, storePriceData } = this.props;
        const { disabled, value } = this.state;

        return (
            <div style={{display:'flex', flexDirection: 'column', flexWrap: 'nowrap', alignItems: 'flex-end'}}>
                <div style={{marginTop:'-3rem', marginBottom: '1rem'}}>
                    <Radio.Group options={options} onChange={this.onChange} value={value}/>
                </div>
                <div style={{marginBottom: '2rem'}}>
                {(value == ' ')?
                    <PriceTextArea
                        style={{textAlign: "right"}}
                        defaultValue={price}
                        placeholder=" ."
                        autoSize={true}
                        onChange={(e) => storePriceData(e.target.value)}
                        disabled={disabled}
                    />:
                    <PriceTextArea
                        style={{textAlign: "center"}}
                        defaultValue={price}
                        placeholder={value}
                        autoSize={true}
                        disabled={disabled}
                    /> 
                }
                </div>
            </div>
        );
  }
}


EditPrice.propTypes = {
    price: PropTypes.string,
    storePriceData: PropTypes.func.isRequired
}

EditPrice.defaultProps = {
    price: ''
}

export default EditPrice;