

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { SendOutlined } from "@ant-design/icons";

import { colors } from "../../../../styles/variables";
import { UitdaTextArea } from "../../../Structure/CommonComponents";


/* Styled Components */
/*        */
const InputBox = styled.div`
    padding: 0.25rem 0.75rem;
    width: 100%;
    min-height: 2.5rem;

    border: 1px solid ${colors.gray_line};
    border-radius: 1.25rem;

    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
`;

    /* TextArea   div  */
    const TextAreaBox = styled.div`
        flex: 1 0;
    `;

    /*     */
    const SendButtonBox = styled.div`
        flex: 0 0;
        padding-bottom: 4px;
    `;

        /*   */
        const SendButton = styled(SendOutlined)`
            cursor: pointer;
        `;


/* React Component */
class ChatInputBox extends Component {

    /* Enter   , handleSendMessage   */
    _handleEnterKey = (e) => {
        const { shiftKey, keyCode } = e;
        if ( keyCode === 13 && !shiftKey ) {    // enter , shift  
            e.preventDefault();                 //    X
            this._handleSendMessage();          // enter   
        }
    }

    /* Chatting Message   */
    _handleSendMessage = () => {
        const { 
            chatSocket, 
            curUser, currentRoom,
            chatInputData,
        } = this.props;

        /*     socket    */
        if ( chatInputData.text ) {
            chatSocket.emit('chat message', {
                message: chatInputData.text, 
                room_id: currentRoom.id,
                email: curUser.email
            })
        }
    }

    render () {

        const { storeChatInputData } = this.props;

        return (
            <InputBox>
                <TextAreaBox 
                onKeyDown={ e => this._handleEnterKey(e) } 
                >
                    <UitdaTextArea 
                        size='100%' 
                        isUnderLine={false} 
                        placeHolder=' ...'

                        data_key='text'
                        storeDataFunc={storeChatInputData}
                    />
                </TextAreaBox>

                <SendButtonBox>
                    <SendButton onClick={this._handleSendMessage} />
                </SendButtonBox>
            </InputBox>
        )
    }
}

ChatInputBox.propTypes = {
    chatSocket: PropTypes.object.isRequired,            //  socket
    curUser: PropTypes.object.isRequired,               //   
    currentRoom: PropTypes.object.isRequired,           //   
    chatInputData: PropTypes.object.isRequired,         //   

    storeChatInputData: PropTypes.func.isRequired,      //     
}

export default ChatInputBox;