////////////////////////////////////////////////////////////////////////////
/* '/manage/update/:board/:id'       (GET)  */

export function getUpdatePostRequest(board, id) {
    return (dispatch) => {

        /* get   url */
        const GETurl = `/api/${board}/update/${id}`;

        /* get   */
        return axios.get(GETurl)

        /* , data  (,  ,  )    dispatch */
        .then(res => res.data)
        .then(data => {
            dispatch(getUpdatePostSuccess(data.post));
        })

        /* , err   dispatch */
        .catch(err => {
            dispatch(getUpdatePostFailure(err))
        })
    }
}

export function getUpdatePostSuccess(post) {
    return {
        type: MANAGE_EDIT_GET_POST_SUCCESS,     // edit  get  
        title: post.title,
        price: post.price,
        description: post.description,
        filelist: post.filelist,
    }
}

export function getUpdatePostFailure(err) {
    return {
        type: MANAGE_EDIT_GET_POST_FAILURE,     // edit  get  
        err,
    }
}


/////////////////////////////////////////////////////////////////////
/* '/manage/edit/' POST       
   id   update/create  (id  update,  create) */

export function EditPostRequest (board, title, price, description, files, id, deletedFileIDs) {
    return (dispatch) => {

        /* POST    url */
        const POSTurl = id ?
        `/api/${board}/update/${id}`:   // id  update
        `/api/${board}/create`;         // id  create

        /* POST   Form Data */
        const formData = new FormData();
        formData.append('title', title);
        if(board==='market'){
            formData.append('price', price);
        }
        formData.append('description', description);
        files.forEach(file => {
            /*  file   Object ,
               originFileObj  File   */
            const FILE_TYPE = id ? 'added' : 'userfile';
            formData.append(FILE_TYPE, file.originFileObj);
        });

        /* update , delete_id list  */
        if (id) { formData.append('deleted_files', deletedFileIDs) }

        /* POST       */
        const successAction = id ?
        updatePostSuccess :
        createPostSuccess ;

        /* POST       */
        const failureAction = id ?
        updatePostFailure :
        createPostFailure ;

        /* create POST  */
        return axios.post(POSTurl, formData, {
            headers: { 'Content-Type': 'multipart/form-data'}
        })

        /* ,      dispatch */
        .then(res => {dispatch(successAction())})
        .catch(err => {dispatch(failureAction(err))})
    }
}

