//  : /pages/manage.js

import React, { Component } from 'react';
import { connect } from 'react-redux'
import PropTypes from 'prop-types';
import { Modal } from 'antd'
import { withLastLocation } from 'react-router-last-location';
import { Redirect } from 'react-router-dom';

import { MARKET, NETWORKING, CARPOOL } from "../constants/categories";
import { EditBody, EditHeader } from "../components/Edit";
import { 
    initEditPage,
    selectEditCategory,
    getUpdatePostRequest,
    EditPostRequest,
    storeEditTitleData,
    storeEditPriceData,
    addFileData,
    deleteFileData,
    storeEditDescriptionData,
    editClickB,
    editClickI,
    editClickU,
    editClickS,
    editSelectTextAlign,
    storeCarpoolData,
    postCarpoolEvent,
} from '../store/actions/manage'
import { initCalenderEvents, selectDate } from '../store/actions/carpool'
import { getStatusRequest } from "../store/actions/auth";


const { confirm } = Modal;

class EditContainer extends Component {

    state = { didMount: false }

    componentDidMount() {

        const {
            isNew,
            match,
            lastLocation,

            getStatusRequest,
            initEditPage,
            getPostRequest,
            selectEditCategory
        } = this.props;

        /*    ! */
        initEditPage();
        getStatusRequest();

        /*    (create), last path  default    */
        if(isNew) {
            const lastPath = lastLocation ? lastLocation.pathname : '';
            const defaultCategory = this._getDefaultCategoryForCreate(lastPath);

            console.log(lastPath)
            console.log(defaultCategory)

            selectEditCategory(defaultCategory);
        }

        /*      (update), get  update    +   */
        else {
            const {
                boardName, id
            } = match.params;

            getPostRequest(boardName, id);
            selectEditCategory(boardName);
        }
        

        // window.onpopstate = (e) => {
        //     e.preventDefault();
        //     console.log(window.history)
        //     confirm({
        //         title: ' ?',
        //         content: 'Ok      .',
        //         onOk: () => { window.history.go(0) },
        //         onCancel: () => { window.history.forward() }
        //     }) 
        // }

        // console.log(window);

        // setInitFalse('');

        // window.addEventListener("beforeunload", this._onUnload);

        this.setState({
            didMount: true
        })
    }

    componentWillUnmount() {
        // window.removeEventListener("beforeunload", this._onUnload);
    }

    /* Create , pathname  default category   
       /manage/posts/networking -> NETWORKING
       /carpool                 -> CARPOOL
                       -> MARKET */
    _getDefaultCategoryForCreate = (pathname) => {

        switch (pathname) {
            case '/manage/posts/networking':
                return NETWORKING;

            case '/carpool':
                return CARPOOL;

            /* /manage/posts/networking     
                MARKET  */
            default:
                return MARKET;
        }
    }

    _onUnload = (e) => {
        e.preventDefault();
        e.returnValue = '';
    }

    _onBackButton = (e) => {
        e.preventDefault();
        console.log(e);
        confirm({
            title: ' ?',
            content: 'Ok      .',
            onOk: () => { window.history.go(0) },
            onCancel: () => { window.history.forward() }
        }) 
    }


    render() {

        const { didMount } = this.state

        const {
            curUser,
            isGetStatusDone,

            /* create / update  props */
            isNew,
            match,
            
            /* App States */
            isEditGetSuccess,
            editCategory,

            title,
            price,
            files,
            deletedFileIDs,
            description,
            editSuccess,
            edit_spanStyle,
            edit_textAlign,
            selectedDate,
            eventsToRenderObj,
            roomInfoData,

            /* App Methods */
            EditPostRequest,
            selectEditCategory,
            storeEditTitleData,
            storeEditPriceData,
            addFileData,
            deleteFileData,
            storeEditDescriptionData,
            editClickB,
            editClickI,
            editClickU,
            editClickS,
            editSelectTextAlign,
            initCalenderEvents,
            selectDate,
            storeCarpoolData,
            postCarpoolEvent
        } = this.props;

        const id = isNew ? 0 : match.params.id;

        /*   
           create  init   isEditInit,
           update  get    isEditGetSuccess
           load  boolean  isLoad  */
        let isLoad = isNew ? isGetStatusDone && didMount : isGetStatusDone && isEditGetSuccess

        return(
            isLoad ?

                curUser ?
                <div>
                    <EditHeader 
                        isNew={isNew}                       // Create / Update 
                        editCategory={editCategory}         //       (default )
                        id={id}                             // Update    id

                        title={title}                       // Edit   Title 
                        price={price}
                        files={files}                       // Edit    
                        deletedFileIDs={deletedFileIDs}     //  ,   id 
                        description={description}           // Eidt   Description 
                        editSuccess={editSuccess}           // Edit   

                        edit_spanStyle={edit_spanStyle}     // BIUS   
                        edit_textAlign={edit_textAlign}     // p text align 

                        selectedDate={selectedDate}         // Carpool    
                        roomInfoData={roomInfoData}         //   

                        /* Methods */
                        selectEditCategory={selectEditCategory}
                        EditPostRequest={EditPostRequest}
                        editClickB={editClickB}
                        editClickI={editClickI}
                        editClickU={editClickU}
                        editClickS={editClickS}
                        editSelectTextAlign={editSelectTextAlign}

                        postCarpoolEvent={postCarpoolEvent}         // Carpool Event  Post Action
                    />
                    <EditBody 
                        editCategory={editCategory}                         // Edit 

                        /* property */
                        title={title}                                       // Edit   Title 
                        price={price}
                        files={files}                                       // Edit    
                        description={description}                           // Edit   Description 

                        eventsObj={eventsToRenderObj}                       //       
                        selectedDate={selectedDate}                         // Carpool    

                        /* methods */
                        storeTitleData={storeEditTitleData}
                        storePriceData={storeEditPriceData}
                        addFileData={addFileData}
                        deleteFileData={deleteFileData}
                        storeDescriptionData={storeEditDescriptionData}

                        initCalenderEvents={initCalenderEvents}             //     events  
                        selectDate={selectDate}                             // Carpool    
                        storeCarpoolData={storeCarpoolData}
                    />
                </div> : 
                <Redirect to='/' /> :
            'loading'
        )
    }

}

EditContainer.propTypes = {
    isNew: PropTypes.bool,      //    
    match: PropTypes.object,    // url   
}

EditContainer.defaultProps = {
    isNew: false,
    match: undefined,
}


const mapStateToProps = (state) => {
    return {
        curUser: state.auth.user,                           //     
        isGetStatusDone: state.auth.isGetStatusDone,        // get status   

        isEditGetSuccess: state.manage.isEditGetSuccess,    // update    
        isEditInit: state.manage.isEditInit,                // Edit    
        isModified: state.manage.isModified,                //    
        editSuccess: state.manage.editSuccess,              //   
        editCategory: state.manage.editCategory,            // edit   (market, networking, carpool)

        title: state.manage.editedTitle,                    // Title Data
        price: state.manage.editedPrice,
        files: state.manage.editedFiles,                    // File List Data
        deletedFileIDs: state.manage.deletedFileIDs,        //   ID 
        description: state.manage.editedDescription,        // Description Data
        
        edit_spanStyle: state.manage.edit_spanStyle,        // BUIS   
        edit_textAlign: state.manage.edit_textAlign,        // p text align  

        selectedDate: state.carpool.selectedDate,           //     
        eventsToRenderObj: state.carpool.eventsToRenderObj, //       
        roomInfoData: state.manage.carpool_RoomInfoData,    //   
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getStatusRequest: () => dispatch(getStatusRequest()),                                   //     

        initEditPage: () => {dispatch(initEditPage())},
        selectEditCategory: (category) => {dispatch(selectEditCategory(category))},             // edit   
        EditPostRequest: (board, title, price, discription, files, id, deletedFileIDs) => {
            dispatch(EditPostRequest(board, title, price, discription, files, id, deletedFileIDs))
        },
        getPostRequest: (board, id) => dispatch(getUpdatePostRequest(board, id)),

        storeEditTitleData: (title) => dispatch(storeEditTitleData(title)),
        storeEditPriceData: (price) => dispatch(storeEditPriceData(price)),
        addFileData: (file) => dispatch(addFileData(file)),
        deleteFileData: (file) => dispatch(deleteFileData(file)),
        storeEditDescriptionData: (description) => {
            dispatch(storeEditDescriptionData(description))
        },

        editClickB: () => dispatch(editClickB()),
        editClickI: () => dispatch(editClickI()),
        editClickU: () => dispatch(editClickU()),
        editClickS: () => dispatch(editClickS()),
        editSelectTextAlign: (textAlignAttr) => {
            dispatch(editSelectTextAlign(textAlignAttr))
        },

        /* Edit Carpool Actions */
        selectDate: (category, date) => dispatch(selectDate(category, date)),                   // carpool    
        initCalenderEvents: (category) => dispatch(initCalenderEvents(category)),               //     events  
        storeCarpoolData: (data_key, data_value) => {                                           // Carpool  Room Info Data  
            dispatch(storeCarpoolData(data_key, data_value))
        },
        postCarpoolEvent: (title, departure, destination, start_date, start_time, meeting_place, contact, description) => {         // Carpool Event  Post Action
            dispatch(postCarpoolEvent(title, departure, destination, start_date, start_time, meeting_place, contact, description))
        }
    }
}

export default withLastLocation(connect(mapStateToProps, mapDispatchToProps)(EditContainer));