//  : components/BoardDetail.js

import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { InfoTextBox } from "./HeaderBox_Sub";
import { colors } from "../../../styles/variables";
import { UserPhoto } from "../../Structure/CommonComponents";
import { MARKET } from '../../../constants/categories';


/* Styled Components */

/* HeaderBox   div  */
const HeaderArea = styled.div`
    position: relative;
    
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: 2rem;
    border-bottom: ${props => props.isPhoto ? `1px solid ${colors.light_gray_line}` : ''};

    display: flex;
    flex-flow: column nowrap;
`;

    /*  , ,      */
    const InfoBox = styled.div`
        height: 4.5rem;
        width: 100%;
        
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
    `;

        /*  User  */
        const UserPhotoBox = styled.div`
            height: 4.5rem;
            width: 4.5rem;
            position: relative;
            border-radius: 50%;         /*     */
            border: ${colors.light_gray_line} solid 2px;

            margin-right: 1rem;
            flex: 0, 4.5rem;
        `;

    /*  */
    const Title = styled.h1`
        margin: 0;
        margin-top: 2rem;

        font-size: 2rem;
    `

    /*  */
    const Price = styled.h3`
        margin: 0;
        margin-top: 0.5rem;

        align-self: flex-end;
        font-size: 1.375rem;
    `;


/* React Component */
const HeaderBox = ({postId, board, isPhoto, isOwner, title, user, created, condition, price, headerMethods}) => {

    const { username, pic_location } = user;

    return (
        <HeaderArea isPhoto={isPhoto} >
            <InfoBox>
                <UserPhotoBox>
                    <UserPhoto imgURL={pic_location} />
                </UserPhotoBox>
                <InfoTextBox
                    board={board} postId={postId}
                    username={username} created={created}
                    condition={condition} isOwner={isOwner}
                    headerMethods={headerMethods}
                />
            </InfoBox>
            <Title>{title}</Title>
            { board === MARKET && <Price>{price} </Price> } 
        </HeaderArea>
    )
}

HeaderBox.propTypes = {
    postId: PropTypes.number.isRequired,        //  id
    isPhoto: PropTypes.bool.isRequired,         //    
    isOwner: PropTypes.bool.isRequired,         //    
    board: PropTypes.string.isRequired,         //  
    title: PropTypes.string.isRequired,         //  
    user: PropTypes.object.isRequired,          //   
    created: PropTypes.string.isRequired,       //  
    condition: PropTypes.string.isRequired,     //  

    price: PropTypes.string,                    // Market    

    headerMethods: PropTypes.object.isRequired,  //   header   method  
}

export default HeaderBox;