

import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { UserPhoto } from "../../../Structure/CommonComponents";
import { colors } from "../../../../styles/variables";


/* Styled Components */
/*      (PhotoContainer + MessageContainer) */
const MessageArea = styled.div`
    margin-top: 0.5rem;
    width: 100%;

    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start;
    justify-content: flex-start;
`;

    /*    style */
    const PhotoContainer = styled.div`
        margin-top: 0.5rem;
        margin-right: 0.5rem;
        height: 1.5rem;
        width: 1.5rem;
    `;

    /*       .
         row-reverse,   row. */
    const MessageContainer = styled.div`
        flex: 1;

        display: flex;
        flex-direction: ${props => props.isMine ? 'row-reverse' : 'row'};
        flex-wrap: nowrap;
        align-items: flex-end;
        justify-content: flex-start;

        cursor: default;
    `;

        /*       */
        const MessageBox = styled.div`
            padding: 0.625rem 0.75rem;

            color: ${props => props.isMine ? `${colors.white}` : `${colors.font_default}`};

            border-radius: 0.75rem;
            background-color: ${props => props.isMine ? `${colors.blue}` : `${colors.gray_bg}`};
        `;

        /*   (  /   )   */
        const AdditionInfoBox = styled.div`
            margin: 0.25rem 0.5rem;
            width: 5.5rem;

            display: flex;
            flex-wrap: nowrap;
            flex-direction: ${props => props.isMine ? 'row-reverse' : 'row'};
            align-items: center;
        `;

            /*     */
            const CreatedTime = styled.div`
                white-space: nowrap;
                font-size: 0.75rem;
                color: ${colors.font_lightgray};
            `;

            /*   ,     */
            const UnreadBall = styled.div`
                margin: 0 0.5rem;
                width: 0.25rem;
                height: 0.25rem;

                border-radius: 50%;
                background-color: ${ props => props.isUnread ? `${colors.blue}` : 'inherit' };
            `;




/* React Component */
const ChatMessage = ({isMine, opntUser, description, created, isUnread}) => {

    const { pic_location } = opntUser;

    const isPhoto = !isMine;

    return (
        <MessageArea isMine={isMine} >
            <PhotoContainer> 
            {
                isPhoto ?
                <UserPhoto size={24} imgURL={pic_location} />:
                '' 
            }
            </PhotoContainer> 

            <MessageContainer isMine={isMine} >
                <MessageBox isMine={isMine} >
                    {description}
                </MessageBox>

                <AdditionInfoBox isMine={isMine} >
                    <CreatedTime>  12:30 </CreatedTime> 
                    <UnreadBall isUnread={isUnread} />
                </AdditionInfoBox>
            </MessageContainer>
        </MessageArea>
    )
}

ChatMessage.propTypes = {
    isMine: PropTypes.bool.isRequired,          //  
    opntUser: PropTypes.object.isRequired,      //   ,     
    // description
    // created
    isUnread: PropTypes.bool.isRequired,        //   
}

export default ChatMessage;