

import React, { Component } from 'react';
import styled from 'styled-components';
import { Route } from 'react-router-dom';
import PropTypes from 'prop-types';

import {
    ErrorPage,
    ManagePost
} from '../ManageContents';

/* Constants */
import {
    MANAGE_ACCOUNT,
    MANAGE_POSTS_MARKET,
    MANAGE_POSTS_NETWORKING,
    // MANAGE_COMMENTS,
    // MANAGE_LIKEPOSTS,
    // MANAGE_CONTACT,
} from '../../../../constants/manage_category'

import {
    MARKET,
    NETWORKING
} from '../../../../constants/categories'

/* Styled Components */

/* Content   div  */
const ContentBoxArea = styled.div`
    margin: 0;
    padding: 0;

    flex: 4;
`;


class ManageContentArea extends Component {


    /* Manage      */
    _renderContent = (kind) => {

        let component,      // Content Component
            isPost,         // Post
            board;          // Market or Networking

        const {
            /* Posts */
            postList,
            deletePost,
            updatePostCondition
        } = this.props;

        switch (kind) {
            case MANAGE_ACCOUNT:
                break;

            case MANAGE_POSTS_MARKET:
                board = MARKET;
                // eslint-disable-next-line
            case MANAGE_POSTS_NETWORKING:
                board = board ? MARKET : NETWORKING;
                isPost = true;
                component = ManagePost;
                break;

            // case MANAGE_COMMENTS:
            //     break;
            //     // eslint-disable-next-line
            // case MANAGE_LIKEPOSTS:
            //     break;
            //     // eslint-disable-next-line
            // case MANAGE_CONTACT:
            //     break;
            //     // eslint-disable-next-line

            default:
                component = ErrorPage;
                break;
        }

        // console.log(component, isPost, board);

        return isPost ?
        
        /*    ,     */
        <Route component={() => {
            return <ManagePost 
                        board={board} 
                        postList={postList}
                        deletePost={deletePost}
                        updatePostCondition={updatePostCondition}
                    />
        }} /> :

        /*       */
        <Route component={component} />
    }


    render() {

        const {
            isLoading,
            kind
        } = this.props
    
        return (
            <ContentBoxArea>
            {
                isLoading ?
                <div>loading</div> :
                this._renderContent(kind)
            }
            </ContentBoxArea>
        )
    }
}

ManageContentArea.propTypes = {
    isLoading: PropTypes.bool.isRequired,               // Manage   loading 
    kind: PropTypes.string.isRequired,                  //   

    postList: PropTypes.array,                          // Posts  
    deletePost: PropTypes.func.isRequired,              // Post  
    updatePostCondition: PropTypes.func.isRequired,     //    
}

export default ManageContentArea;