/*   window   min-height, min-width    
   Manage, Edit, Board, Detail, Carpool, Chatting  ,
   ContentComponent  , min-height, min-width custom    . */

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { colors } from "../../../styles/variables";


/* Styled Components */
const BackgroundArea = styled.div`
    padding-top: ${props => props.doesHaveHeader ? '4rem' : '0' };
    height: 100%;
    width: 100%;
    min-height: ${props => props.heightValue}px;
    min-width: ${props => props.widthValue}px;

    background-color: ${colors.gray_bg};
`;



/* React Component */
class BackgroundTemplate extends Component {

    state = {}

    componentDidMount() {
        window.addEventListener('resize', this._updateWindowSize);
        this._updateWindowSize()
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this._updateWindowSize);
    }

    _updateWindowSize = () => {
        this.setState({
            windowHeight: window.innerHeight,
            windowWidth: window.innerWidth,
        })
    }

    render() {

        const { windowHeight, windowWidth } = this.state;
        const { 
            doesHaveHeader,
            ContentComponent,
            customHeight, customWidth 
        } = this.props;

        let heightValue = customHeight ? customHeight : windowHeight;
        let widthValue = customWidth ? customWidth : windowWidth;

        return (
            <BackgroundArea 
                doesHaveHeader={doesHaveHeader} 
                heightValue={heightValue} 
                widthValue={widthValue} 
            >
                <ContentComponent />
            </BackgroundArea>
        )
    }
}


BackgroundTemplate.propTypes = {
    doesHaveHeader: PropTypes.bool.isRequired,      //   
    ContentComponent: PropTypes.func.isRequired,    // Background    

    customHeight: PropTypes.number,                 // window Height     
    customWidth: PropTypes.number,                  // window Width     
}

BackgroundTemplate.defaultProps = {
    customHeight: 0,
    customWidth: 0,
}

export default BackgroundTemplate;