

//  : pages/Carpool

import React, { Component } from "react";
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';

import SideBar from "../components/Structure/SideBar";
import CarpoolBoard from "../components/Carpool";
import { 
    selectDate,
    getCarpoolEvents,
    initCalenderEvents, changeClosedEvents,
    renderTotalEvents, renderMyEvents,
    storeClickedEventData,
    postDeleteEventRequest,
    storeEventUpdateData, postUpdateEventRequest,
    postCloseOrCancleEventRequest,
    postJoinEventRequest, postCancleJoinEventRequest,
} from "../store/actions/carpool";
import { topicSelect } from "../store/actions/topic";
import { getStatusRequest } from "../store/actions/auth";
import { CARPOOL } from "../constants/categories";

class CarpoolContainer extends Component {    

    componentDidMount() {
        const { topicSelect, getCarpoolEvents, getStatusRequest } = this.props;

        topicSelect(CARPOOL);
        getCarpoolEvents();
        getStatusRequest();
    }

    render() {
        const { 
            curUser,
            isGetStatusDone,
            isCarpoolGetDone,

            totalOrMyOption,
            eventsToRenderObj,
            selectedDate, 
            eventsOnSelectedDate,
            selectedEvent,
            eventDataToUpdate,
        
            initCalenderEvents,
            renderTotalEvents,
            renderMyEvents,
            changeClosedEvents,
            selectDate,
            storeClickedEventData,
            deleteEvent,
            storeEventUpdateData,
            updateEvent,
            closeOrCancleEvent,
            joinEvent,
            cancleJoinEvent,
        } = this.props;

        const isLoaded = isGetStatusDone && isCarpoolGetDone;

        return (
            isLoaded ?
            
                curUser ?

                <div>
                    <SideBar topic={CARPOOL} />

                    <CarpoolBoard 
                        curUser={curUser}
                        totalOrMyOption={totalOrMyOption}
                        eventsObj={eventsToRenderObj}
                        selectedDate={selectedDate}
                        eventsOnSelectedDate={eventsOnSelectedDate}
                        selectedEvent={selectedEvent}
                        eventDataToUpdate={eventDataToUpdate}

                        initCalenderEvents={initCalenderEvents}
                        renderTotalEvents={renderTotalEvents}
                        renderMyEvents={renderMyEvents}
                        changeClosedEvents={changeClosedEvents}
                        selectDate={selectDate}
                        storeClickedEventData={storeClickedEventData}
                        deleteEvent={deleteEvent}
                        storeEventUpdateData={storeEventUpdateData}
                        updateEvent={updateEvent}
                        closeOrCancleEvent={closeOrCancleEvent}
                        joinEvent={joinEvent}
                        cancleJoinEvent={cancleJoinEvent}
                    /> 
                </div> :
            
                <Redirect to='/' />:

            ''
        )
                    
        
    }
}

const mapStateToProps = (state) => {
    return {
        curUser: state.auth.user,
        isGetStatusDone: state.auth.isGetStatusDone,                // get status   

        isCarpoolGetDone: state.carpool.isGetSuccess,               // carpool     
        
        totalOrMyOption: state.carpool.totalOrMyOption,
        eventsToRenderObj: state.carpool.eventsToRenderObj,

        selectedDate: state.carpool.selectedDate,
        eventsOnSelectedDate: state.carpool.eventsOnSelectedDate,
        selectedEvent: state.carpool.selectedEvent,
        eventDataToUpdate: state.carpool.eventDataToUpdate,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        topicSelect: (topic) => {dispatch(topicSelect(topic))},                             // App topic state CARPOOL 
        getStatusRequest: () => {dispatch(getStatusRequest())},                             //     

        getCarpoolEvents: () => dispatch(getCarpoolEvents()),                               //    get  

        initCalenderEvents: (category) => dispatch(initCalenderEvents(category)),           //     events  
        renderTotalEvents: () => {dispatch(renderTotalEvents())},                           //    
        renderMyEvents: () => dispatch(renderMyEvents()),                                   //    
        changeClosedEvents: (isHidden) => dispatch(changeClosedEvents(isHidden)),           //    / 

        selectDate: (category, date) => {dispatch(selectDate(category, date))},             // Carpool  date   
        storeClickedEventData: (id) => {dispatch(storeClickedEventData(id))},               //     

        deleteEvent: (id) => {dispatch(postDeleteEventRequest(id))},                        //   
        storeEventUpdateData: (key, value) => dispatch(storeEventUpdateData(key, value)),   //     
        updateEvent: (id, eventData) => {dispatch(postUpdateEventRequest(id, eventData))},  //   

        closeOrCancleEvent: (eventID, condition) => {                                       //     
            dispatch(postCloseOrCancleEventRequest(eventID, condition))
        },
        joinEvent: (eventID) => {dispatch(postJoinEventRequest(eventID))},                  //    
        cancleJoinEvent: (guestID) => {dispatch(postCancleJoinEventRequest(guestID))},      //      
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CarpoolContainer);