

import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import { 
    MarketIcon,
    NetworkingIcon,
    CarpoolIcon,
    ManageIcon,
    ChattingIcon,
} from "../../../../styles/images/Menu_Icon_Image";
import { 
    MARKET, NETWORKING, CARPOOL, MANAGE, CHATTING
} from "../../../../constants/categories";
import { colors } from "../../../../styles/variables";


/* Styled Components */
/* Item   Link Box.
   hover   ,
      border  . */
const MenuItemLinkBox = styled(Link)`
    margin: 0;
    padding-left: 3.7rem;
    height: 3rem;
    width: 100%;

    border-right: ${props => props.isSelected ?
                    '4px solid #1E90FF' : 'none'};

    display: flex;
    flex-flow: row nowrap;
    align-items: center;

    :hover {
        background-color: #F4F4F4;
    }
`;

/* Menu Icon   div  */
const MenuIconImg = styled.div`
    margin-right: 0.75rem;
    height: 1.5rem;
    width: 1.5rem;

    background-image: ${props => `url(${props.imgURL})`};
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
`;

/* Menu   */
const MenuText = styled.div`
    color: ${colors.black};
    font-size: 1.15rem;
`;


/* Custom Functions */
/* category   img   */
const categoryToImg = (category) => {
    switch (category) {
        case MARKET:
            return MarketIcon;
        case NETWORKING:
            return NetworkingIcon;
        case CARPOOL:
            return CarpoolIcon;
        case MANAGE:
            return ManageIcon;
        case CHATTING:
            return ChattingIcon;
        default: return;
    }
}

/* React Component */
const MenuItem = ({titleText, url, category, isSelected}) => {
    
    const imgURL = categoryToImg(category);
    
    return (
        <MenuItemLinkBox to={url} isSelected={isSelected} >
            <MenuIconImg imgURL={imgURL} />
            <MenuText> {titleText} </MenuText>
        </MenuItemLinkBox>
    )
}

MenuItem.propTypes = {
    titleText: PropTypes.string.isRequired,     //   title text 
    url: PropTypes.string.isRequired,           //    url
    category: PropTypes.string.isRequired,      // react app  category constant 
    isSelected: PropTypes.bool,                 //   
}

MenuItem.defaultProps = {
    isSelected: false
}

export default MenuItem;