///////////////////////

/*     */
const BoardOrderBox = styled.div`
    margin-top: 1rem;
    width: 100%;
    
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;  
`;

/*    */
const BoardOrder = styled.p`
    margin: 0 0.5rem;

    /* font  */
    color: ${props => {
        return props.isSelected ? colors.font_darkgray : colors.font_lightgray
    }};

    cursor: pointer;
`;

//////////////////////////////////////

/* React Component */
class ManagePost extends Component{

    state = {
        curOrder: 1     //   
    }

    /*       */
    _onClickOrder = (order) => {
        /*   order  state curOrder  */
        this.setState({
            ...this.state,
            curOrder: order
        })
    }

    _handleDelete = (board, id) => {
        const { deletePost } = this.props;
        
        deletePost(board, id)
        window.location.reload();
    }

    /* Post Item render  */
    _renderPostItems = (postList, board) => {

        const { updatePostCondition } = this.props;

        return postList.map((post, idx) => {
            // console.log(post);

            /* posting  URL */
            const postURL = `/board/${board}/${post.id}`;

            /*   edit  URL */
            const editURL = `/manage/edit/${board}/${post.id}`;

            return (
                <PostItem isLast={(idx + 1) === postList.length} key={idx}>
                    <PostID>{post.postId}</PostID>
                    
                    <TextBox>
                        <PostTitle> <TitleLink to={postURL} >{post.title}</TitleLink> </PostTitle>
                        <PostSubInfo> {post.created} </PostSubInfo>
                    </TextBox>
                    
                    {/* , ,   */}
                    <ButtonBox>
                        <PostManageButton href={editURL} ></PostManageButton>
                        <PostManageButton onClick={() => this._handleDelete(board, post.id)} isDelete={true} ></PostManageButton>
                        <ConditionSelector 
                            board={board} 
                            post_id={post.id}
                            originCondition={post.condition} 
                            updatePostCondition={updatePostCondition} 
                        />
                    </ButtonBox>
                </PostItem>
            )
        })
    }

    /*   render  */
    _renderOrders = (curOrder, maxOrder) => {
        /*     ([1, 2, ... maxOrder]) */
        let orderList = [...Array(maxOrder).keys()].map(x => x + 1);

        return orderList.map((order, idx) => {
            return <BoardOrder
                        isSelected={order === curOrder} 
                        onClick={() => this._onClickOrder(order)}
                        key={idx} 
                    >
                        {order}
                    </BoardOrder>
        })
    }


    render() {

        const MAX_ITEM = 7;     //      

        const {
            board,
            postList,
            deletePost,
        } = this.props;

        const {
            curOrder,
        } = this.state;

        /*     */
        let postsNum = postList.length;
        // let postsNum = 0;    // post   
        
        /*    */
        let maxOrder = Math.ceil(postsNum / MAX_ITEM);

        /*   */
        let title = (board === 'market') ? '' : '';

        /*       */
        let parsedList = postList.slice((curOrder - 1) * MAX_ITEM, curOrder * MAX_ITEM);

        /* parse list    */
        parsedList.forEach((post, i) => {
            post.postId = postsNum - ((curOrder - 1) * MAX_ITEM) - i
        })

        return (
            <WholeBox>
                <HeaderBox>
                    <Title>{title}  </Title>
                    <SubInfo>{postsNum} </SubInfo>
                    <CreateButton to='/manage/edit/newpost' ></CreateButton>
                </HeaderBox>
                <BodyBox>
                    {
                        /* post  PostBox,  NoPostBox */
                        postsNum ?
                        <PostsBox>
                            {
                                this._renderPostItems(parsedList, board, deletePost)
                            }
                            {
                                /* maxOrder 1  BoardOrderBox render */
                                maxOrder > 1 ?
                                <BoardOrderBox>
                                    {
                                        this._renderOrders(curOrder, maxOrder)
                                    }
                                </BoardOrderBox> :
                                ''
                            }
                        </PostsBox> :

                        <NoPostBox>
                               .<br /><br />
                                   !
                        </NoPostBox>
                    }
                </BodyBox>
            </WholeBox>
        )
    }

}

ManagePost.propTypes = {
    postList: PropTypes.array,                          //  
    board: PropTypes.string.isRequired,                 //   

    deletePost: PropTypes.func.isRequired,              // Post  
    updatePostCondition: PropTypes.func.isRequired,     //    
}

ManagePost.defaultProps = {
    postList: []
}


export default ManagePost