


import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { BGTemplate } from '../../styles/templates/manage'

import { SideBar, ContentArea } from './BodyComponents'


/* Styled Components */

/* Body Area  div 
   backgrond color ,     */
const BodyArea = styled(BGTemplate)`
    margin: 0;
    padding: 2.5rem;
    padding-top: 6.5rem;

    /*   50rem  ,    */
    min-height: ${props => {
        return props.windowHeight > 50 * 16 ?
        `${props.windowHeight}px` :
        '50rem';
    }};
    min-width: 1230px; /*      (1150px + 5rem) */
`;

/* Body   (SideBox, ContentBox)   Box
      , margin auto    
     . (  mobile   )
   
   Side Box Content Box  flex box*/
const WholeBox = styled.div`
    width: 1150px;
    margin: 0 auto;

    display: flex;
    flex-flow: row nowrap;
`


/* React Component */
class ManageBody extends Component {


    render () {

        const {
            curUser,
            kind,
            windowHeight,
            isLoading,

            /* posts */
            postList,
            deletePost,
            updatePostCondition,
        } = this.props

        return (
            <BodyArea windowHeight={windowHeight} >
                <WholeBox>
                    <SideBar
                        curUser={curUser}
                        kind={kind}
                    />
                    <ContentArea 
                        isLoading={isLoading}
                        kind={kind}
                        postList={postList}
                        deletePost={deletePost}
                        updatePostCondition={updatePostCondition}
                    />
                </WholeBox>
            </BodyArea>
        )
    }
}

ManageBody.propTypes = {
    curUser: PropTypes.object.isRequired,               //    
    kind: PropTypes.string.isRequired,                  //   
    windowHeight: PropTypes.number.isRequired,          //     
    isLoading: PropTypes.bool.isRequired,               // Manage   loading 

    postList: PropTypes.array,                          // Posts  
    deletePost: PropTypes.func.isRequired,              // Post  
    updatePostCondition: PropTypes.func.isRequired,     //    
}

export default ManageBody;