

import React, { Component } from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import { colors } from "../../../styles/variables";
import { UserPhoto } from "../../Structure/CommonComponents";


/* Styled Components */
/*   size */
const WholeArea = styled.div`
    width: 100%;
    height: 4.5rem;
`;

    /* Item  Wrapper   */
    const ItemWrapperStyle = css`
        width: 100%;
        height: 100%;
    `;

    /*    Link  */
    const LinkWrapper = styled(Link)`
        ${ItemWrapperStyle}
        color: ${colors.font_default};

        :hover {
            color: ${colors.font_default};
            background-color: ${colors.gray_bg};
        }
    `;

    /*     div */
    const SelectedWrapper = styled.div`
        ${ItemWrapperStyle}
        background-color: ${colors.gray_bg};
    `;

        /* Wrapper   Item Content Box  */
        const ItemContainer = styled.div`
            width: 100%;
            height: 100%;
            padding: 0.5rem 1rem;

            background-color: inherit;

            display: flex;
            flex-flow: row nowrap;
            align-items: center;
        `;

            /*    ,      Box */
            const TextBox = styled.div`
                margin-left: 1rem;
                flex: 1;
                height: 100%;

                overflow: hidden;

                display: flex;
                flex-flow: column nowrap;
                align-items: flex-start;
                justify-content: center;
            `;

                const OpntUserName = styled.div`
                    font-weight: bold;
                `;

                const MessageText = styled.div`
                    width: 100%;
                    
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                `;

                const MessageSkeleton = styled.div`
                    height: 22px;
                `;

            /*   , unread   Box */
            const SubInfoBox = styled.div`
                flex: 0 0;
                margin-left: 0.5rem;
                
                display: flex;
                flex-flow: column nowrap;
                align-items: flex-end;
                justify-content: center;
            `;

                const UpdatedInfo = styled.div`
                    font-size: 0.75rem;
                    white-space: nowrap;
                `;

                const UnreadInfo = styled.div`
                    margin-top: 0.25rem;
                    padding: 0.25rem 0.25rem;
                    height: 1.25rem;
                    min-width: 1.25rem;

                    color: ${colors.white};
                    font-size: 0.75rem;

                    border-radius: 0.625rem;
                    background-color: ${colors.badge_red};

                    display: flex;
                    align-items: center;
                    justify-content: center;
                `;

                const UnreadSkeleton = styled.div`
                    margin-top: 0.25rem;
                    height: 1.25rem;
                `;



/* React Component */
class RoomListItem extends Component {

    render() {

        const { 
            chatSocket,
            curRoomID,
            isSelectedRoom, 
            opntUser, 
            lastChat,
            updated, 
            unread, 
        } = this.props;

        const { id, username, pic_location } = opntUser;

        const roomURL = `/chatting/t/${id}`;

        return (
            <WholeArea>
            {
                isSelectedRoom ?

                <SelectedWrapper>
                    <ItemContentBox 
                        pic_location={pic_location} username={username}
                        lastChat={lastChat}updated={updated} unread={unread}
                    />
                </SelectedWrapper> :

                <LinkWrapper to={roomURL} onClick={() => { 
                    if (curRoomID) { chatSocket.emit('room out', {room_id: curRoomID}) }    // curRoomID 0   
                }} >
                    <ItemContentBox 
                        pic_location={pic_location} username={username}
                        lastChat={lastChat} updated={updated} unread={unread}
                    />
                </LinkWrapper>
            }
            </WholeArea>
        )
    }
}

const ItemContentBox = ({pic_location, username, lastChat, updated, unread}) => {
    return (
        <ItemContainer>
            <UserPhoto imgURL={pic_location} size={48} />
            <TextBox>
                <OpntUserName>{username}</OpntUserName>
                {
                    lastChat ?
                    <MessageText>{lastChat}</MessageText> :
                    <MessageSkeleton />
                }
            </TextBox>
            <SubInfoBox>
                <UpdatedInfo>{updated}</UpdatedInfo>
                {
                    unread ? 
                    <UnreadInfo>{unread}</UnreadInfo> :
                    <UnreadSkeleton />
                }
                {/* <UnreadInfo>{unread}</UnreadInfo> */}
            </SubInfoBox>
        </ItemContainer>
    )
}


RoomListItem.propTypes = {
    chatSocket: PropTypes.object.isRequired,        //  socket
    curRoomID: PropTypes.number.isRequired,         //    id 
    isSelectedRoom: PropTypes.bool.isRequired,      //    
    opntUser: PropTypes.object.isRequired,          //   
    updated: PropTypes.string.isRequired,           //     
    unread: PropTypes.number.isRequired,            //   
    lastChat: PropTypes.string,                     //             

    getChatData: PropTypes.func.isRequired,         //   , chatting data  
}

RoomListItem.defaultProps = {
    lastChat: '',
}


export default RoomListItem