

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { EditBoard, EditCarpool } from './EditBodyComponents'
import { BGTemplate } from '../../styles/templates/manage'
import { CARPOOL } from '../../constants/categories'

/* Styled Components */
/*   div  */
const BackGround = styled(BGTemplate)`
    padding-top: 4rem;
    min-height: ${props => {
        return `${props.minHeight}px`
    }};
`;

/* React Component */
class EditBody extends Component {

    state = {}

    componentDidMount() {
        window.addEventListener('resize', this._updateWindowSize);
        this._updateWindowSize()
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this._updateWindowSize);
    }

    _updateWindowSize = () => {
        this.setState({
            ...this.state,
            windowHeight: window.innerHeight
        })
    }

    render() {

        const { 
            windowHeight,
        } = this.state;

        const {
            editCategory,

            title,
            price,
            files,
            description,
            selectedDate,
            eventsObj,

            storeTitleData,
            storePriceData,
            addFileData,
            deleteFileData,
            storeDescriptionData,
            initCalenderEvents,
            selectDate,
            storeCarpoolData
        } = this.props;

        return (
            <BackGround minHeight={windowHeight} >
            {
                editCategory === CARPOOL ?

                <EditCarpool windowHeight={windowHeight} 
                    initCalenderEvents={initCalenderEvents}
                    selectedDate={selectedDate} selectDate={selectDate}
                    eventsObj={eventsObj}
                    storeCarpoolData={storeCarpoolData}
                /> :
                
                <EditBoard minHeight={windowHeight}
                    editCategory={editCategory}
                    title={title} storeTitleData={storeTitleData}
                    price={price} storePriceData={storePriceData}
                    files={files} addFileData={addFileData} deleteFileData={deleteFileData}
                    description={description} storeDescriptionData={storeDescriptionData}
                />
            }
            </BackGround>
        )
    }
}

EditBody.propTypes = {
    editCategory: PropTypes.string.isRequired,          // Edit Category
    
    title: PropTypes.string,                            // Edit   Title 
    price: PropTypes.string,
    files: PropTypes.array,                             // Edit    
    description: PropTypes.string,                      // Eidt   Description 

    selectedDate: PropTypes.object,                     // Carpool    
    eventsObj: PropTypes.object.isRequired,             //       

    /* Methods */
    storeTitleData: PropTypes.func.isRequired,          // Title  App State  
    storePriceData: PropTypes.func.isRequired,
    addFileData: PropTypes.func.isRequired,             // Files  App State  
    deleteFileData: PropTypes.func.isRequired,          // App State        
    storeDescriptionData: PropTypes.func.isRequired,    // Description  App State  

    initCalenderEvents: PropTypes.func.isRequired,      //     events  
    selectDate: PropTypes.func.isRequired,              // Carpool    
    storeCarpoolData: PropTypes.func.isRequired,        // Carpool  Room Info Data  
}

EditBody.defaultProps = {
    title: '', 
    price: '',                           
    files: [],                             
    description: '',

    selectedDate: {},
}

export default EditBody;