

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { CloseOutlined } from "@ant-design/icons";

import { RoomInfoBox } from "./RoomModal_Sub";
import { colors } from "../../../styles/variables";
import './ant-modal.css';

/* Styled Components */
const WholeArea = styled.div`
    min-height: 30rem;
`;

/*     */
const CloseButton = styled.div`
    position: absolute;
    right: 0;
    top: 0;
    padding: 1rem;

    color: ${colors.white};
    font-size: 1.5rem;

    cursor: pointer;
`;

/* React Component */
class RoomBox extends Component {

    render() {
        
        const { 
            curUser,
            selectedEvent, 
            eventDataToUpdate,

            cancleModal, 
            deleteEvent,
            storeEventUpdateData,
            updateEvent,
            closeOrCancleEvent,
            joinEvent,
            cancleJoinEvent,
        } = this.props

        return (
            <WholeArea>
                <RoomInfoBox 
                    curUser={curUser}
                    selectedEvent={selectedEvent}
                    eventDataToUpdate={eventDataToUpdate}

                    deleteEvent={deleteEvent}
                    storeEventUpdateData={storeEventUpdateData}
                    updateEvent={updateEvent}
                    closeOrCancleEvent={closeOrCancleEvent}
                    joinEvent={joinEvent}
                    cancleJoinEvent={cancleJoinEvent}
                />
                <CloseButton onClick={cancleModal} ><CloseOutlined/></CloseButton>
            </WholeArea>
        )
    }
}

RoomBox.propTypes = {
    curUser: PropTypes.oneOfType([                         //   
        PropTypes.number, PropTypes.object
    ]).isRequired,
    selectedEvent: PropTypes.object.isRequired,         //   
    eventDataToUpdate: PropTypes.object.isRequired,     //     

    cancleModal: PropTypes.func.isRequired,             //    
    deleteEvent: PropTypes.func.isRequired,             //   
    storeEventUpdateData: PropTypes.func.isRequired,    //       
    updateEvent: PropTypes.func.isRequired,             //   
    closeOrCancleEvent: PropTypes.func.isRequired,      //      
    joinEvent: PropTypes.func.isRequired,               //    
    cancleJoinEvent: PropTypes.func.isRequired,         //     
}

export default RoomBox;