

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Button } from "antd";
import { MoreOutlined } from "@ant-design/icons";

import renderPopoverContent from "./renderPopoverContent";
import { UserPhoto, UitdaPopover } from "../../../Structure/CommonComponents";
import { colors } from "../../../../styles/variables";
import { 
    CLOSED, ACTIVE, OWNER, OWNER_CLOSED, GUEST, GUEST_CLOSED 
} from '../../../../constants/calendar_consts'

/* Styled Components */

/*  Info Box (, , ,  ) */
const HeaderInfoBox = styled.div`
    padding: 1rem;
    /* margin-bottom: 1rem; */
    min-height: 4rem;

    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start;
    justify-content: space-between;
`;

    /* Head Left Box. MoreSee Button       */
    const HLBox = styled.div`
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
    `;

        const UserCreatedInfoBox = styled.div`
            /* flex: 1; */
            margin-left: 1rem;

            display: flex;
            flex-flow: column nowrap;
        `;

            const UserName = styled.div`
                margin-bottom: 0.25rem;
                
                font-size: 1rem;
                font-weight: bold;
            `;

            const Created = styled.div`
                font-size: 0.625rem;
            `;

    const HRBox = styled.div`
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
    `;

        const StateLabel = styled.div`
            margin-right: 1rem;

            color: ${colors.font_gray};
            cursor: default;

            display: flex;
            flex-flow: row nowrap;
            align-items: center;
        `;

            const LabelCircle = styled.div`
                height: 0.5rem;
                width: 0.5rem;
                margin-right: 0.75rem;
                
                border-radius: 50%;

                background-color: ${props => props.labelColor};
            `;

        const MoreIcon = styled(MoreOutlined)`
            color: ${colors.font_gray};
            font-size: ${16}px;

            cursor: pointer;
        `;


        const CancleUpdateButton = styled(Button)`
            margin-right: 1rem;

            :hover {
                color: ${colors.font_darkgray};
            }
        `;


/* React Component */
class RoomInfoHeaderBox extends Component {
    
    /* label      */
    _getColorAndText = () => {
        
        const { label } = this.props.selectedEvent;
        
        switch (label) {
            case CLOSED: return {labelColor: colors.closed_gray, labelText: ''};
            case ACTIVE: return {labelColor: colors.active_blue, labelText: ' '};
            case OWNER: 
            case OWNER_CLOSED:  return {labelColor: colors.owner_yellow, labelText: ''};
            case GUEST: 
            case GUEST_CLOSED:  return {labelColor: colors.guest_green, labelText: ''};
            default:  return {labelColor: colors.closed_gray, labelText: ''}
        }
    }

    _handleUpdate = () => {
        const {
            selectedEvent, 
            eventDataToUpdate, 
            updateEvent, changeModeToRead
        } = this.props;

        const { id } = selectedEvent;

        updateEvent(id, eventDataToUpdate);
        changeModeToRead();
    }


    render() {

        const {
            isUpdateMode, 
            
            curUser,
            selectedEvent,

            changeModeToRead,
            deleteEvent, changeModeToUpdate,
            closeOrCancleEvent,
            joinEvent, cancleJoinEvent
        } = this.props

        const { user, created } = selectedEvent

        const clickMethods = {
            deleteEvent, changeModeToUpdate,
            closeOrCancleEvent,
            joinEvent, cancleJoinEvent
        };
        const MoreButtonContentList = renderPopoverContent(curUser, selectedEvent, clickMethods);

        const { labelColor, labelText } = this._getColorAndText();

        return (
            <HeaderInfoBox>
                <HLBox>
                    <UserPhoto size={40} />
                    <UserCreatedInfoBox>
                        <UserName>{user ? user.username : ''}</UserName>
                        <Created>{created}</Created>
                    </UserCreatedInfoBox>
                </HLBox>
                
                {
                    isUpdateMode ?
    
                    <HRBox>
                        <CancleUpdateButton shape='round' onClick={changeModeToRead} ></CancleUpdateButton>
                        <Button shape='round' type='primary' onClick={this._handleUpdate} ></Button>
                    </HRBox> :
    
                    <HRBox>
                        <StateLabel>
                            <LabelCircle labelColor={labelColor} />
                            {labelText}
                        </StateLabel>
                        <UitdaPopover 
                            contentList={MoreButtonContentList} 
                            PopButton={() => <MoreIcon /> } 
                        />
                    </HRBox>
                }
    
            </HeaderInfoBox>
        )
    }
}

RoomInfoHeaderBox.propTypes = {
    curUser: PropTypes.oneOfType([                         //   
        PropTypes.number, PropTypes.object
    ]).isRequired,

    isUpdateMode: PropTypes.bool.isRequired,            //   

    selectedEvent: PropTypes.object.isRequired,         //   
    id: PropTypes.number.isRequired,                    //    id
    user: PropTypes.object.isRequired,                  //     
    created: PropTypes.string.isRequired,               //    
    label: PropTypes.string.isRequired,                 //    
    eventDataToUpdate: PropTypes.object.isRequired,     //     

    deleteEvent: PropTypes.func.isRequired,             //   
    changeModeToUpdate: PropTypes.func.isRequired,      //    
    changeModeToRead: PropTypes.func.isRequired,        //    
    updateEvent: PropTypes.func.isRequired,             //   
    closeOrCancleEvent: PropTypes.func.isRequired,      //      
    joinEvent: PropTypes.func.isRequired,               //    
    cancleJoinEvent: PropTypes.func.isRequired,         //     
}

export default RoomInfoHeaderBox;