

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Divider } from "antd";

import BaseCalendar from "../../../Structure/Base_Calendar";
import {
    // Calendar,
    TitleInput,
    PlaceInputBox,
    TimePickerBox,
    SubInfoInputBox,
    DescriptionBox,
} from './Subcomponents';
import { colors } from '../../../../styles/variables'
import { MANAGE } from "../../../../constants/categories";
import { CONTACT, MEETING_PALCE } from "../../../../constants/edit_RoomInfo_DataKeys";


/* Styled Components */

/* Header     div  */
const WholeArea = styled.div`
    min-height: ${props => props.windowHeight - 64}px;

    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
`;

/* Calendar Box RoomInfo Box   */
const ContentBoxTemplate = styled.div`
    margin: 0 2rem;
    height: ${props => props.boxHeight}px;
    min-height: 553px;  /*   1377 x 768      */
    padding: 2rem;
    
    /*   padding margin  */
    @media (max-width: 1500px) {
        margin: 0 1rem;
        padding: 1rem;
    }

    background-color: ${colors.white};

    display: flex;
    flex-flow: column nowrap;
`;

/*   div  */
const CalendarBox = styled(ContentBoxTemplate)`
    min-width: 50rem;
    width: ${props => props.boxHeight * 1.43 }px;
`;

    /* ContentBox padding      div */
    const CalendarContainer = styled.div`
        flex: 1;
    `

/*     div  */
const RoomInfoBox = styled(ContentBoxTemplate)`
    min-width: 24rem;
    width: ${props => props.boxHeight * 0.73 }px;
`;

/* margin  Divider */
const StyledDivider = styled(Divider)`
    margin: 1rem 0;
    /* padding: 0 0.5rem; */
`;



/* React Component */
class EditCarpool extends Component {

    state = {}

    componentDidMount() {
        this.setState({
            isLoaded: true
        })
    }

    render () {

        const { isLoaded } = this.state;

        const {
            windowHeight,
            selectedDate,
            eventsObj,

            initCalenderEvents,
            selectDate,
            storeCarpoolData
        } = this.props;

        const height = windowHeight - 192;    //   padding +   (8rem)   

        return ( isLoaded ?
            <WholeArea windowHeight={windowHeight} >
                <CalendarBox boxHeight={height} >    
                    <CalendarContainer>
                        <BaseCalendar 
                            category={MANAGE}

                            eventsObj={eventsObj}
                            selectedDate={selectedDate}

                            initCalenderEvents={initCalenderEvents}
                            selectDate={selectDate}
                        />
                    </CalendarContainer>
                </CalendarBox>

                <RoomInfoBox boxHeight={height} >
                    <TitleInput storeCarpoolData={storeCarpoolData} />
                    <StyledDivider />
                    <PlaceInputBox storeCarpoolData={storeCarpoolData} />
                    <TimePickerBox storeCarpoolData={storeCarpoolData} />
                    <SubInfoInputBox dataKey={CONTACT} storeCarpoolData={storeCarpoolData} />
                    <SubInfoInputBox dataKey={MEETING_PALCE} storeCarpoolData={storeCarpoolData} />
                    <DescriptionBox storeCarpoolData={storeCarpoolData} />
                </RoomInfoBox>
            </WholeArea> :
            ''
        )
    }
}

EditCarpool.propTypes = {
    windowHeight: PropTypes.number.isRequired,          //   
    selectedDate: PropTypes.object,                     // Carpool    
    eventsObj: PropTypes.object.isRequired,             //       

    initCalenderEvents: PropTypes.func.isRequired,      //     events  
    selectDate: PropTypes.func.isRequired,              // Carpool    
    storeCarpoolData: PropTypes.func.isRequired,        // Carpool  Room Info Data  
}


export default EditCarpool;