//  : pages/Home

import React, { Component } from "react";
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';  

import { getStatusRequest } from "../store/actions/auth";
import EnterPage from "../components/Home";

class HomeContainer extends Component {    

    componentDidMount() {
        const { getStatusRequest } = this.props;
        getStatusRequest();
    }

    render() {

        const { curUser, isGetStatusDone } = this.props;

        return isGetStatusDone ?
            <div>
            {
                curUser ?
                <Redirect to='/board/market' /> :
                <EnterPage />
            }
            </div> :
            'loading'
    }
}

const mapStateToProps = (state) => {
    return { 
        curUser: state.auth.user,                               //     
        isGetStatusDone: state.auth.isGetStatusDone,            // get status   
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getStatusRequest: () => dispatch(getStatusRequest()),       //           
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(HomeContainer);