

import React from "react";
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import { UserPhoto } from "../../../../../Structure/CommonComponents";
import { colors } from "../../../../../../styles/variables";
import { MARKET } from '../../../../../../constants/categories'


/* Styled Components */
/* Content Box  div  */
const ContentBoxDiv = styled.div`
    padding: 1.5rem;
    padding-top: .75rem;

    display: flex;
    flex-flow: column nowrap;
`;

/* description     Header Box */
const ContentHead = styled.div`
    position: relative;
    padding-bottom: 2.75rem;
    border-bottom: 1px solid ${colors.light_gray_line};

    display: flex;
    flex-flow: column nowrap;
`;

    /* User Photo, User, Created, Condition  
       row direction div  */
    const InfoBox = styled.div`
        position: relative;
        height: 2.5rem;
        width: 100%;
        margin-bottom: 1.5rem;
        
        font-size: .875rem;
        color: ${colors.font_gray};

        display: flex;
        flex-flow: row nowrap;

        ${props => !props.isPhotoCard && css`
            margin-top: 1.5rem;
            margin-bottom: 2.5rem;
        `}
    `;

        /* UserPhoto   style  div  */
        const UserPhotoBox = styled.div`
            position: relative;
            top: -1.25rem;
            flex: 0 4rem;
            height: 4rem;

            background-color: white;
            border-radius: 50%;
            border: ${colors.gray_line} solid 1px;
            
            display: flex;
            align-items: center;
            justify-content: center;
        `;

        /* Info Header    div Box */
        const Informations = styled.div`
            height: 100%;
            flex: 1;

            display: flex;
            flex-flow: row nowrap;
            align-items: flex-start;
            justify-content: space-between;
        `;

            /* UserName Created   div Box (column direction) */
            const UserCreatedInfoBox = styled.div`
                margin-left: 1rem;

                display: flex;
                flex-flow: column nowrap;
            `;

                const UserName = styled.div`
                    margin-bottom: 0.25rem;
                    height: 1.25rem;
                    
                    line-height: 1.25rem;
                    font-size: 1rem;
                    font-weight: bold;
                `;

                const Created = styled.div`
                    height: 1.25rem;

                    line-height: 1.25rem;
                `;

            /*    StatusCircle + StatusText */
            const StatusBox = styled.div`
                cursor: default;

                display: flex;
                flex-flow: row nowrap;
                align-items: center;
            `;

                const StatusCircle = styled.div`
                    height: 0.5rem;
                    width: 0.5rem;
                    margin-right: 0.5rem;

                    border-radius: 50%;
                    background-color: ${props => props.labelColor};
                `;

    /*   +    Link */
    const TitleLink = styled(Link)`
        margin-bottom: 1rem;
        
        color: ${colors.black};
        text-decoration: none;
        font-size: 1.5rem;
        font-weight: bold;
        line-height: 1.75rem;
    `;

    /*    div  */
    const PriceBox = styled.div`
        align-self: flex-end;

        font-size: 1.125rem;
        line-height: 1.25rem;
        font-weight: bold;
    `;

/* Description  Box */
const DescriptionBox = styled.div`
    margin-top: 1.5rem;
    height: 4.5rem;
    
    line-height: 1.5rem;
    
    /*      (...) */
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow-y: hidden;
    text-overflow: ellipsis;

    ${props => !props.isPhotoCard && css`
        margin-top: 2rem;
        height: 13.5rem;
        -webkit-line-clamp: 9;
    `}
`;


/* Custom Functions */
const _getLabelColor = (condition) => {
    switch (condition) {
        case ' ': case ' ': return colors.active_blue;
        case ' ': return colors.owner_yellow;
        case ' ': case '': return colors.closed_gray;
        default: return;
    }
}

/* React Component */
const ContentBox = ({isPhotoCard, boardName, postId, title, user, created, condition, description, price}) => {

    /*  url */
    const postURL = `/board/${boardName}/${postId}`;    

    const { username, pic_location } = user;
    const labelColor = _getLabelColor(condition);

    return (
        <ContentBoxDiv>
            <ContentHead>
                <InfoBox isPhotoCard={isPhotoCard} >
                    <UserPhotoBox>
                        <UserPhoto imgURL={pic_location} />
                    </UserPhotoBox>
                    
                    <Informations>
                        <UserCreatedInfoBox>
                            <UserName>{username}</UserName>
                            <Created>{created}</Created>
                        </UserCreatedInfoBox>
                        <StatusBox> <StatusCircle labelColor={labelColor} /> {condition} </StatusBox>
                    </Informations>
                </InfoBox>
                <TitleLink to={postURL} > {title} </TitleLink>
                {
                    boardName === MARKET &&
                    <PriceBox>{price}</PriceBox>
                }
            </ContentHead>
            <DescriptionBox isPhotoCard={isPhotoCard} >
                {description}
            </DescriptionBox>
        </ContentBoxDiv>
    )
}

ContentBox.propTypes = {
    isPhotoCard: PropTypes.bool.isRequired, // Photo Card   
    boardName: PropTypes.string.isRequired, //  
    postId: PropTypes.number.isRequired,    //   DB  id
    title: PropTypes.string.isRequired,     // 
    user: PropTypes.object.isRequired,      //   
    created: PropTypes.string.isRequired,   //  
    condition: PropTypes.string,            //    ( ,   ...)
    description: PropTypes.string,          //  

    price: PropTypes.string,                // [market]  
}

export default ContentBox;