/*  Middle Component       
   Manage Chatting   */

import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom'

import { colors } from '../../../styles/variables'


/* Styled Compoents */
/*    */
const ComponentArea = styled.div`
    height: 4rem;
    flex: 1;
    padding-left: 2rem;

    display: flex;
    flex-flow: row nowrap;
    align-items: center;

    color: ${colors.font_darkgray};
    font-size: 0.9375rem;
`; 

/*    */
const VirticleLine = styled.div`
    height: 2rem;
    width: 1px;
    background-color: ${colors.gray_line};

    margin-left: 3rem;
`;

/*     */
const CategoryLink = styled(Link)`
    margin-left: 3rem;
    
    text-decoration: none;
    color: ${colors.font_darkgray};
`;


/* react component */
const LinkCategory = ({categoryDatas}) => {
    return (
        <ComponentArea>
             
            <VirticleLine />
            {
                categoryDatas.map((data, idx) => {
                    const { url, text } = data
                    return <CategoryLink to={url} key={idx} >{text}</CategoryLink>
                })
            }
        </ComponentArea>
    )
}

LinkCategory.propTypes = {
    categoryDatas: PropTypes.array.isRequired,       //  , url   Array 
}

export default LinkCategory;