import React, { Component } from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import { Select, Tooltip, Divider } from 'antd';
import {  } from "@ant-design/icons";
import { 
    MdFormatAlignJustify,
    MdFormatAlignLeft,
    MdFormatAlignCenter,
    MdFormatAlignRight,
} from 'react-icons/md'
import { Redirect } from 'react-router-dom';

import { CategorySelectBox, RightBox } from './Subcomponenets';
import { colors } from '../../../styles/variables'; 
import { TEXT_ALIGN } from '../../../constants/edit_funcs'
import { CARPOOL } from '../../../constants/categories'

const { Option } = Select;

/* Styled Compoents */
/*     */
const HeaderBox = styled.div`
    height: 4rem;
    flex: 1;

    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
`; 

const LeftBox = styled.div`
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
`

    /*       */
    const EditFuncBox = styled.div`
        margin: 0 2rem;
        height: 100%;

        display: flex;
        flex-flow: row nowrap;
        align-items: center;
    `;

        const FontSizeSelect = styled(Select)`
            margin: 0 0.5rem !important;
        `;

        /* Icon   (ant-design) */
        const IconStyle = css`
            margin: 0 0.5rem;
            font-size: 1rem;

            cursor: pointer;
            transition-property: color, background-color;
            transition-duration: 0.3s;

            color: ${colors.icon_darkgray};
            background-color: ${colors.white};

            /*  ,    */
            ${props => props.isSelected && css`
                color: ${colors.white};
                background-color: ${colors.icon_darkgray};
            `}
        `;

        /* Ant Design Icon */
        // const EditIcon = styled(Icon)`
        //     ${IconStyle}
        // `;

        /* Ant Design   */
        const AlignJustifyIcon = styled(MdFormatAlignJustify)`${IconStyle}`;
        const AlignLeftIcon = styled(MdFormatAlignLeft)`${IconStyle}`;
        const AlignCenterIcon = styled(MdFormatAlignCenter)`${IconStyle}`;
        const AlignRightIcon = styled(MdFormatAlignRight)`${IconStyle}`;



/* react component */
class EditComponent extends Component {

    state = {}

    componentDidMount () {
        const { defaultBoard } = this.props;

        this.setState({
            ...this.state,
            board: defaultBoard,
        })
    }

    /* Select Box Handling  */
    _handleSelect = (value) => {
        const { selectEditCategory } = this.props;

        selectEditCategory(value);
    }

    
    render () {

        const { 
            isNew,
            id,

            editCategory,
            editSuccess,

            title,
            price,
            files,
            deletedFileIDs,
            description,

            // spanStyle,
            textAlign,

            selectedDate,
            roomInfoData,

            /* Methods */
            selectEditCategory,

            EditPostRequest,

            // editClickB,
            // editClickI,
            // editClickS,
            // editClickU,
            selectTextAlign,

            postCarpoolEvent,
        } = this.props;

        const isCarpool = editCategory === CARPOOL;

        const redirerctURL = isCarpool ?
        '/carpool' :
        `/manage/posts/${editCategory}`;

        const TooltipProps = {
            mouseEnterDelay: 0,
            mouseLeaveDelay: 0
        }

        // console.log(spanStyle);
        // console.log(`title: ${title}, files: ${files}, description: ${description}`);
        // console.log(deletedFileIDs)
        // console.log(id);
        // console.log(files);

        return (
            editSuccess ? 
            <Redirect to={redirerctURL} /> :    //   , Redirect
            <HeaderBox>
                
                <LeftBox>
                    <CategorySelectBox 
                        isNew={isNew}
                        editCategory={editCategory}
                        selectEditCategory={selectEditCategory}
                    />

                    {
                        /* Carpool  EditFuncBox 
                        EditFuncBox Subcomponents   */
                        editCategory === CARPOOL ?
                        '' :
                        <EditFuncBox>
                            {/* font-size */}
                            <Tooltip title=' ' {...TooltipProps} >
                                <FontSizeSelect
                                    defaultValue={10} 
                                >
                                    <Option value={8} >8 pt</Option>
                                    <Option value={9} >9 pt</Option>
                                    <Option value={10} >10 pt</Option>
                                    <Option value={12} >12 pt</Option>
                                    <Option value={14} >14 pt</Option>
                                    <Option value={18} >18 pt</Option>
                                    <Option value={24} >24 pt</Option>
                                </FontSizeSelect>
                            </Tooltip>
                            
                            {/*  ? */}

                            <Divider type='vertical' />

                            {/* <Tooltip title='' {...TooltipProps} >
                                <EditIcon type="bold" isSelected={spanStyle.bSelect} onClick={editClickB} />
                            </Tooltip>
                            <Tooltip title='' {...TooltipProps} >
                                <EditIcon type="italic" isSelected={spanStyle.iSelect} onClick={editClickI} />
                            </Tooltip>
                            <Tooltip title='' {...TooltipProps} >
                                <EditIcon type="underline" isSelected={spanStyle.uSelect} onClick={editClickU} />
                            </Tooltip>
                            <Tooltip title='' {...TooltipProps} >
                                <EditIcon type="strikethrough" isSelected={spanStyle.sSelect} onClick={editClickS} />
                            </Tooltip>

                            <Tooltip title='' {...TooltipProps} >
                                <EditIcon type="font-colors" />
                            </Tooltip> */}
                            
                            <Divider type="vertical" />
                            
                            {/* p   (text-align) */}
                            <Tooltip title=' ' {...TooltipProps} >
                                <AlignJustifyIcon 
                                    isSelected={textAlign === TEXT_ALIGN.justify} 
                                    onClick={() => selectTextAlign(TEXT_ALIGN.justify)}    
                                />
                            </Tooltip>
                            <Tooltip title=' ' {...TooltipProps} >
                                <AlignLeftIcon 
                                    isSelected={textAlign === TEXT_ALIGN.left} 
                                    onClick={() => selectTextAlign(TEXT_ALIGN.left)}
                                />
                            </Tooltip>
                            <Tooltip title=' ' {...TooltipProps} >
                                <AlignCenterIcon 
                                    isSelected={textAlign === TEXT_ALIGN.center} 
                                    onClick={() => selectTextAlign(TEXT_ALIGN.center)}
                                />
                            </Tooltip>
                            <Tooltip title=' ' {...TooltipProps} >
                                <AlignRightIcon 
                                    isSelected={textAlign === TEXT_ALIGN.right} 
                                    onClick={() => selectTextAlign(TEXT_ALIGN.right)}
                                />
                            </Tooltip>
                        </EditFuncBox>
                    }
                </LeftBox>

                <RightBox 
                    isCarpool={isCarpool} 
                    isNew={isNew}
                    id={id}
                    editCategory={editCategory}

                    title={title}
                    price={price}
                    files={files}
                    deletedFileIDs={deletedFileIDs}
                    description={description}

                    selectedDate={selectedDate}
                    roomInfoData={roomInfoData}

                    EditPostRequest={EditPostRequest}
                    postCarpoolEvent={postCarpoolEvent}
                />
                
            </HeaderBox>
        )
    }
}

EditComponent.propTypes = {
    editCategory: PropTypes.string.isRequired,      // Default Category 

    isNew: PropTypes.bool,                          // Create / Update 
    id: PropTypes.number,                           // Update    id
    editSuccess: PropTypes.bool.isRequired,         //    

    /*     */
    title: PropTypes.string.isRequired,             // Title Data
    price: PropTypes.string.isRequired,
    files: PropTypes.array.isRequired,              // Files Data
    deletedFileIDs: PropTypes.array.isRequired,     //  ,   id 
    description: PropTypes.string.isRequired,       // Description Data

    spanStyle: PropTypes.object.isRequired,         // BIUS   
    textAlign: PropTypes.string.isRequired,         // p text align  

    /* Carpool   */
    selectedDate: PropTypes.object,                 // Carpool    
    roomInfoData: PropTypes.object,                 // Carpool  

    /* Methods */
    selectEditCategory: PropTypes.func.isRequired,  //   
    EditPostRequest: PropTypes.func.isRequired,     // Post Create / Update function

    editClickB: PropTypes.func.isRequired,
    editClickI: PropTypes.func.isRequired,
    editClickU: PropTypes.func.isRequired,
    editClickS: PropTypes.func.isRequired,
    selectTextAlign: PropTypes.func.isRequired,     // text align   

    postCarpoolEvent: PropTypes.func.isRequired,    // Carpool Event  Post Action
}

EditComponent.defaultProps = {
    isNew: true,
    id: 0,
}

export default EditComponent;