import React, {Component} from 'react'
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';

import { colors } from "../../../styles/variables";
import Logo from "../../Structure/CommonComponents/Logo";
import SearchBar from "./SearchBar";
import './Header.css'

/* Styled Components */

/* Header   */
const HeaderBox = styled.div`
    height: 4rem;
    width: 100%;
    padding: 0;
    
    position: fixed;
    top: 0;
    z-index: 2021;

    background-color: ${colors.blue};

    display: flex;
    flex-flow: row nowrap;

    opacity: 1;
    visibility: visible;

    transition: visibility 0.5s, 
                opacity 0.5s;

    ${props => !props.isHeaderOn && css`
        opacity: 0;
        visibility: hidden;
    `}
`;

/*     */
const LogoContainer = styled.div`
    flex: 0 15rem;

    display: flex;
    justify-content: center;
    align-items: center;
`

/*     */
const SearchBarContainer = styled.div`
    flex: 1;
    height: 4rem;
    
    display: flex;
    justify-content: center;
    align-items: center;
`;

/* */
const UserInfoContainer = styled.div`
    flex: 0 8rem;
`;


/* React Component */

class BoardHeader extends Component {

    render() {

        const {
            isHeaderOn,
            isLoggedIn,
            board,

            handleLogout,
            getBoardRequest
        } = this.props;

        return (
            <HeaderBox isHeaderOn={isHeaderOn} >
                
                <LogoContainer>
                    <Logo isWhite={true} />
                </LogoContainer>

                <SearchBarContainer>
                    <SearchBar 
                        getBoardRequest={getBoardRequest} 
                        board={board}
                    /> 
                </SearchBarContainer>

                <UserInfoContainer>
                {
                    isLoggedIn ? 
                    <a onClick={handleLogout} href='/' className='LogItem'>Logout</a> : 
                    <Link to='/auth/login' className='LogItem'>Login</Link>
                }
                </UserInfoContainer>

            </HeaderBox>
        )
    }
}

BoardHeader.propTypes = {
    // Properties
    isHeaderOn: PropTypes.bool.isRequired,      // Header  ,
    isLoggedIn: PropTypes.bool.isRequired,      //  ,
    user: PropTypes.object,                     //  
    board: PropTypes.string.isRequired,         //  topic  

    // Methods
    handleLogout: PropTypes.func.isRequired,    //  
    getBoardRequest: PropTypes.func.isRequired, // board  , get    
}

BoardHeader.defaultProps = {
    user: {},
}

export default BoardHeader;