

import React, { Component } from 'react';
import PropTypes from 'prop-types';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import interactionPlugin from '@fullcalendar/interaction';

import '@fullcalendar/core/main.css';
import '@fullcalendar/daygrid/main.css';
import './Calendar.css';


class Calendar extends Component {

    state = {}

    componentDidMount () {
        const { selectDate } = this.props;

        const today = new Date()

        selectDate(today);
    }


    _selectDate = (info) => {

        console.log(info)
        const { date, dayEl } = info;

        const { prevElem } = this.state;
        const { 
            // selectedDate,
            selectDate,
        } = this.props;

        if ( prevElem ) { prevElem.style.opacity = 0; }

        dayEl.style.opacity = 0.1;

        this.setState({
            ...this.state,
            prevElem: info.dayEl
        })

        selectDate(date);
    }

    render () {

        return (
            <FullCalendar 
                plugins={[ dayGridPlugin, interactionPlugin ]}      // Fullcalendar  
                locale='ko'                                         // : 

                header={{                                           //    
                    left: 'today',
                    center: 'prev, title, next',
                    right: ''
                }}

                height='parent'                                     //    (parent  )
                fixedWeekCount={false}                              //    week   render (6 fix X)

                dateClick={this._selectDate}                        //       
            />
        )
    }
}


Calendar.propTypes = {
    selectedDate: PropTypes.object,             // Carpool    
    selectDate: PropTypes.func.isRequired,      // Carpool    
}

export default Calendar;
