import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import UserBadgeBox from "./UserBadgeBox";
import { Logo } from '../CommonComponents';
import { colors } from '../../../styles/variables'


/* Styled Compoents */
/*      */
const HeaderBox = styled.div`
    height: 4rem;
    background-color: ${ props => props.isBGWhite ? 
    colors.white : colors.blue };

    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    box-shadow: 0 0 5px rgba(0,0,0,.05);
    /* border-bottom: 1px solid ${colors.gray_line}; */

    display: flex;
    flex-flow: row nowrap;
`; 

    /* */
    const ContentBox = styled.div`
        flex: 1;
        height: 100%;

        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
    `;


/* react component */
class BaseHeader extends Component {

    render () {

        const { 
            curUser,
            isBGWhite,
            MiddleComponent,
            doesNeedUserBadge,

            logoutRequest,
        } = this.props;

        return (
            <HeaderBox isBGWhite={isBGWhite} >
                <Logo isWhite={!isBGWhite} />

                <ContentBox>
                    <MiddleComponent />
                    {
                        doesNeedUserBadge ?
                        <UserBadgeBox curUser={curUser} logoutRequest={logoutRequest} /> :
                        ''
                    }
                </ContentBox>
            </HeaderBox>
        )
    }
}

BaseHeader.propTypes = {
    curUser: PropTypes.object,                          //   

    isBGWhite: PropTypes.bool,                          //  
    MiddleComponent: PropTypes.func.isRequired,         // Header   
    doesNeedUserBadge: PropTypes.bool,                  //      

    logoutRequest: PropTypes.func.isRequired,           //  
}

BaseHeader.defaultProps = {
    isBGWhite: true,
    doesNeedUserBadge: true,
    curUser: {},
}

export default BaseHeader;