/*  Room     Popover content data list   */

import React from 'react';
import { 
    DeleteOutlined, EditOutlined, FieldTimeOutlined, CloseCircleOutlined, PushpinOutlined, MessageOutlined
} from "@ant-design/icons";

import { OWNER, OWNER_CLOSED, GUEST, GUEST_CLOSED } from "../../../../constants/calendar_consts";


const renderCarpoolModalPopoverContent = ( curUser, selectedEvent, clickMethods ) => {

    const { id, label, guestlist, user } = selectedEvent;
    const { 
        deleteEvent, changeModeToUpdate,
        closeOrCancleEvent,
        joinEvent, cancleJoinEvent
    } = clickMethods;

    /* ,     (, ) */
    let ownerContentList = [
        {
            icon: <EditOutlined />,
            text: '',
            clickMethod: () => { changeModeToUpdate(); }
        },
        {
            icon: <DeleteOutlined />,
            text: '',
            clickMethod: () => {
                deleteEvent(id);
                
                /*     ,
                   socket  , modal   . */
                setTimeout(() => {
                    window.location.reload();
                }, 300);
            }
        }
    ];

    /*      ( ) */
    let guestContentList = [
        {
            icon: <MessageOutlined />,
            text: ' ',
            type: 'link',
            url: `/chatting/t/${user.id}`
        }
    ]


    switch (label) {

        case OWNER:
            return [
                ...ownerContentList,
                {
                    icon: <FieldTimeOutlined />,
                    text: '',
                    clickMethod: () => { closeOrCancleEvent(id, 2) /*  */ }   
                }
            ]

        case OWNER_CLOSED:
            return [
                ...ownerContentList,
                {
                    icon: <CloseCircleOutlined  />,
                    text: ' ',
                    clickMethod: () => { closeOrCancleEvent(id, 1)   /*   */  }   
                }
            ]

        case GUEST:
        case GUEST_CLOSED:
            return [
                {
                    icon: <CloseCircleOutlined  />,
                    text: '  ',
                    clickMethod: () => {
                        const foundedGuest = guestlist.filter(guest => guest.email === curUser.email);
                        cancleJoinEvent(foundedGuest.id);
                    }
                },
                ...guestContentList
            ]

        default:
            return [
                {
                    icon: <PushpinOutlined />,
                    text: '  ',
                    clickMethod: () => { joinEvent(id) }
                },
                ...guestContentList
            ] 
    }
}


export default renderCarpoolModalPopoverContent;