

import React, { Component } from 'react';
import PropTypes from 'prop-types';

import BaseHeader from "../Structure/BaseHeader";
import EditHeaderComponent from "./EditHeaderComponent";


/* React Component */
class EditHeader extends Component {

    _renderMiddleComponent = () => {
        
        const {
            editCategory,
            isNew, id,
            editSuccess,

            title, price, files, deletedFileIDs, description,
            selectedDate, roomInfoData,

            selectEditCategory,
            EditPostRequest,
            postCarpoolEvent,
        } = this.props
        
        return (
            <EditHeaderComponent 
                editCategory={editCategory}
                isNew={isNew} id={id}
                editSuccess={editSuccess}
            
                title={title} price={price} files={files}
                deletedFileIDs={deletedFileIDs}
                description={description}

                selectedDate={selectedDate}
                roomInfoData={roomInfoData}

                /* Methods */
                selectEditCategory={selectEditCategory}
                EditPostRequest={EditPostRequest}
                postCarpoolEvent={postCarpoolEvent}
            />
        )
    }

    render() {
        return (
            <BaseHeader
                isBGWhite={true}
                MiddleComponent={this._renderMiddleComponent}
                doesNeedUserBadge={false}
            />
        )
    }
}


EditHeader.propTypes = {
    isNew: PropTypes.bool.isRequired,               // Create / Update 
    editCategory: PropTypes.string.isRequired,      //       (default )
    id: PropTypes.number.isRequired,                // Update    id
    editSuccess: PropTypes.bool.isRequired,         // Edit   

    title: PropTypes.string,                        // Edit   Title 
    price: PropTypes.string,
    files: PropTypes.array,                         // Edit    
    deletedFileIDs: PropTypes.array,                //  ,   id 
    description: PropTypes.string,                  // Edit   Description 

    selectedDate: PropTypes.object,                 // Carpool    
    roomInfoData: PropTypes.object,                 //   

    /*  */
    selectEditCategory: PropTypes.func.isRequired,  //   
    EditPostRequest: PropTypes.func.isRequired,     // Board   POST   

    postCarpoolEvent: PropTypes.func.isRequired,    // Carpool Event  Post Action
}

EditHeader.defaultProps = {
    title: '',
    price: '',
    files: [],
    deletedFileIDs: [],
    description: '',

    selectedDate: new Date(),
    roomInfoData: {},
}


export default EditHeader;