//  : pages/manage.js


import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'

/* Components */
import { ManageBody, ManageHeader } from '../components/Manage'

/* Actions */
import {
    getMyPostRequest,               // Posts GET request 
    deletePostRequest,              // Post Delete Post request   
    updatePostConditionRequest,
} from '../store/actions/manage'
import { getStatusRequest, logoutRequest } from '../store/actions/auth'

/* Constants */
import {
    MANAGE_ACCOUNT,
    MANAGE_POSTS_MARKET,
    MANAGE_POSTS_NETWORKING,
    MANAGE_COMMENTS,
    MANAGE_LIKEPOSTS,
    MANAGE_CONTACT,
} from '../constants/manage_category'
import {
    MARKET,
    NETWORKING,
} from '../constants/categories'


class ManageContainer extends Component {

    state = {}

    componentDidMount() {
        const {
            kind,
            getStatusRequest,
            getMyPostRequest
        } = this.props

        let board;

        getStatusRequest();

        switch(kind) {

            /*   */
            case MANAGE_ACCOUNT:
                break;

            /*   */
            case MANAGE_POSTS_MARKET:
                board = MARKET;
                // eslint-disable-next-line
            case MANAGE_POSTS_NETWORKING:
                board = board ? MARKET : NETWORKING;

                // console.log(`posts/${board}`);
                getMyPostRequest(board);
                break;

            /*    */
            case MANAGE_COMMENTS:
                // console.log('comment category');
                break;

            /*    */
            case MANAGE_LIKEPOSTS:
                // console.log('likeposts category');
                break;

            /*    */
            case MANAGE_CONTACT:
                break;

            /* kind post  */
            default:
                break;
        }

        /* window size  ,   state  */
        window.addEventListener('resize', this._updateWindowSize)
        this._updateWindowSize()
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this._updateWindowSize)
    }

    /* window  update */
    _updateWindowSize = () => {
        this.setState({
            ...this.state,
            windowHeight: window.innerHeight
        })
    }


    render() {

        const {
            isGetStatusDone,
            curUser,
            kind,

            isGetManageItemsDone,
            isManageItemsLoading,

            postList,

            logoutRequest,
            deletePostRequest,
            updatePostConditionRequest,
        } = this.props;
    
        const { windowHeight } = this.state;

        const isLoaded = isGetStatusDone && isGetManageItemsDone;

        return(
            <div>
            {
                isLoaded ?
                
                    curUser ? 
                    <div>
                        <ManageHeader
                            curUser={curUser}
                            logoutRequest={logoutRequest}
                        />
                        <ManageBody 
                            curUser={curUser}
                            kind={kind}
                            windowHeight={windowHeight}
                            isLoading={isManageItemsLoading}
                            postList={postList}

                            deletePost={deletePostRequest}
                            updatePostCondition={updatePostConditionRequest}
                        /> 
                    </div> :
                    <Redirect to='/' />:

                ' '
            }
            </div> 
        )
    }

}

ManageContainer.propTypes = {
    kind: PropTypes.string,
}

const mapStateToProps = (state) => {
    return {
        curUser: state.auth.user,                                   //  
        isGetStatusDone: state.auth.isGetStatusDone,                // get status   

        isGetManageItemsDone: state.manage.isGetManageItemsDone,    // Manage  GET   
        isManageItemsLoading: state.manage.isManageItemsLoading,    // Manage     

        postList: state.manage.postList,                            // Posts GET    post list
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getStatusRequest: () => {dispatch(getStatusRequest())},                         //     
        logoutRequest: () => dispatch(logoutRequest()),                                 //  

        getMyPostRequest: (board) => {dispatch(getMyPostRequest(board))},               //    Post  GET request 
        deletePostRequest: (board, id) => {dispatch(deletePostRequest(board, id))},     // Post Delete POST request 
        updatePostConditionRequest: (board, id, condition) => {                         //    POST request 
            dispatch(updatePostConditionRequest(board, id, condition))
        }
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(ManageContainer);