

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { BoxHeaderArea, BoxHeaderTitle } from "../CommonComponents";
import RoomListItem from "./RoomListItem";


/* Styled Components */
/* Header Body   Box style */
const WholeArea = styled.div`
    height: 100%;
    width: 100%;

    display: flex;
    flex-flow: column nowrap;
`;

    /* Room List  Box Style */
    const RoomListBody = styled.div`
        padding: 0.5rem 0;
        flex: 1 0;
        overflow-y: auto;
    `;


/* React Component */
class ContectListBox extends Component {


    _renderRoomList = (roomList) => {
        
        return roomList.map( (room, idx) => {
            const { chatSocket, currentRoom } = this.props;
            const { id, opponent_user, updated, unread, last_chat } = room;

            const curRoomID = currentRoom.id;
            const isSelectedRoom = curRoomID === id;

            return (
                <RoomListItem 
                    chatSocket={chatSocket}
                    curRoomID={curRoomID}
                    isSelectedRoom={isSelectedRoom}
                    opntUser={opponent_user} 
                    updated={updated} 
                    unread={unread} 
                    lastChat={last_chat} 
                    key={idx}
                />
            )
        })
    }

    render() {

        const { roomList, isChatDataGetDone } = this.props;

        return(
            <WholeArea>
                <BoxHeaderArea>
                    <BoxHeaderTitle></BoxHeaderTitle>
                </BoxHeaderArea>

                <RoomListBody>
                {
                    isChatDataGetDone ?
                    this._renderRoomList(roomList) :
                    'loading'
                }
                </RoomListBody>
            </WholeArea>
        )
    }
}

ContectListBox.propTypes = {
    chatSocket: PropTypes.object.isRequired,            //  socket
    roomList: PropTypes.array.isRequired,               //   
    currentRoom: PropTypes.object.isRequired,           //   
    isChatDataGetDone: PropTypes.bool.isRequired,       // Chatting Container mount   GET   

    getChatData: PropTypes.func.isRequired,             //   , chatting data  
}

export default ContectListBox;